import { Dispatch } from "redux";

import { injector } from "../../app";
import { IListenerMiddleWareListener } from "../app-listener.middleware";

import { IGlobalAppState } from "../../interfaces/global-app-state.interface";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import { GoogleLoginService } from "../../services/pure/google-login.service";

import { GOOGLE_LOGIN_INIT_CORDOVA, GOOGLE_LOGIN_INIT_WEB, GOOGLE_LOGIN_OAUTH } from "./google-login.actions";
import { GOOGLE_LOGIN_INIT_OPTIONS } from "../../app.constants";

import {
    googleLoginDisable,
    googleLoginInitWebSuccess, googleLoginLoading, googleLoginLoadScriptError,
    googleLoginLoadScriptSuccess, googleLoginEnable, googleLoginNotLoading, googleLoginAuth2Ready,
    googleLoginInitWebError, googleLoginOAuthSuccess, googleLoginGlobalReady, googleLoginInitCordovaSuccess,
    googleLoginPluginReady, googleLoginInitCordovaError
} from "./google-login.action-creators";
import { appLoading } from "../app.action-creators";
import { auth_signInGoogleLoginWToken } from "../auth/auth.action-creators";

export let googleLoginListener: IListenerMiddleWareListener = {
    [GOOGLE_LOGIN_INIT_WEB]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let googleLoginService = <GoogleLoginService>injector.get('googleLoginService');

        dispatch(googleLoginLoading());
        dispatch(googleLoginDisable());

        googleLoginService.jQueryLoadGoogleLoginSDKScript()
            .done((script, textStatus) => {
                try {
                    dispatch(googleLoginLoadScriptSuccess());

                    if (googleLoginService.getGoogleLoginGlobal()) {
                        dispatch(googleLoginGlobalReady());
                    }

                    googleLoginService.getGoogleLoginGlobal().load('auth2', function () {
                        let auth2Ready = googleLoginService.getGoogleLoginGlobal().auth2.init(GOOGLE_LOGIN_INIT_OPTIONS);
                        if (auth2Ready) {
                            dispatch(googleLoginAuth2Ready());
                        }
                        dispatch(googleLoginInitWebSuccess());
                        dispatch(googleLoginNotLoading());
                        dispatch(googleLoginEnable());
                    });
                } catch (error) {
                    dispatch(googleLoginInitWebError(error));
                    dispatch(googleLoginNotLoading());
                }
            })
            .fail((jqxhr, settings, exception) => {
                dispatch(googleLoginLoadScriptError(exception));
                dispatch(googleLoginNotLoading());
            });
    },
    [GOOGLE_LOGIN_OAUTH]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let googleLoginService = <GoogleLoginService>injector.get('googleLoginService');

        try {
            dispatch(googleLoginLoading());
            dispatch(googleLoginDisable());

            let GoogleAuth = googleLoginService.getGoogleLoginGlobal().auth2.getAuthInstance();

            GoogleAuth.currentUser.listen((GoogleUser) => {
                if (GoogleAuth.isSignedIn.get()) {
                    dispatch(appLoading());
                    dispatch(googleLoginOAuthSuccess());
                    dispatch(auth_signInGoogleLoginWToken(GoogleUser));

                    dispatch(googleLoginNotLoading());
                    dispatch(googleLoginEnable());
                }
            })
        } catch (error) {
            dispatch(googleLoginNotLoading());
            dispatch(googleLoginEnable());
        }
    },
    [GOOGLE_LOGIN_INIT_CORDOVA]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let googleLoginService = <GoogleLoginService>injector.get('googleLoginService');

        dispatch(googleLoginLoading());
        dispatch(googleLoginDisable());

        try {
            if (googleLoginService.getGooglePluginGlobal()) {
                dispatch(googleLoginPluginReady());
                dispatch(googleLoginInitCordovaSuccess());
            } else {
                throw new Error('Google Login plugin not available!')
            }
            dispatch(googleLoginNotLoading());
            dispatch(googleLoginEnable());
        } catch (error) {
            dispatch(googleLoginInitCordovaError(error));
            dispatch(googleLoginNotLoading());
        }
    }
};


