import { Action } from "redux";
import { Dispatch } from "redux";
import { Store } from "redux";
import { IGlobalAppState } from "../interfaces/global-app-state.interface";

export type IListenerMiddleWareListener = { [key: string]: (action: Action, dispatch: Dispatch<any>, state: IGlobalAppState) => any };

export let listenerMiddleWare = (listeners: IListenerMiddleWareListener[]) => (store: Store<IGlobalAppState>) => next => action => {
    // listeners are provided with a picture
    // of the world before the action is applied
    const preActionState: IGlobalAppState = store.getState();

    // release the action to reducers before
    // firing additional actions
    next(action);

    // always async
    setTimeout(() => {
        // can have multiple listeners listening
        // against the same action.type
        listeners.forEach(listener => {
            if (listener[action.type]) {
                listener[action.type](action, store.dispatch, preActionState);
            }
        });
    });
};




