import { Action } from "redux";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";

import {
    DEVICE_SET_DEVICE_TYPE,
    DEVICE_SET_PLATFORM,
    DEVICE_SET_BADGE_NUMBER,
    DEVICE_SET_DEVICE_TOKEN
} from "./device.actions";

export function device_setDeviceType(type: string): ActionWPayload {
    return {
        type: DEVICE_SET_DEVICE_TYPE,
        payload: type
    }
}

export function device_setPlatform(platformName: string = 'web'): ActionWPayload {
    return {
        type: DEVICE_SET_PLATFORM,
        payload: platformName
    }
}

export function device_setDeviceBadge(number: number): ActionWPayload {
    return {
        type: DEVICE_SET_BADGE_NUMBER,
        payload: number
    }
}

export function device_setDeviceToken(token: string): ActionWPayload {
    return {
        type: DEVICE_SET_DEVICE_TOKEN,
        payload: token
    }
}


