import { FirebaseUserModel } from "../../models/firebase-user.model";

import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import { AuthState } from "../app.initial.state";

import {
    AUTH_RESET_TARGET_STATE,
    AUTH_RESET_USER, AUTH_SET_TARGET_STATE, AUTH_SET_USER, AUTH_SET_USER_ALREADY_LOGGED_IN, AUTH_SET_USER_NOT_LOGGED_IN,
    AUTH_SIGN_IN_ADD_INVALID_EMAIL, AUTH_SIGN_IN_EMAIL,
    AUTH_SIGN_IN_EMAIL_ERROR_WRONG_PASSWORD,
    AUTH_SIGN_IN_REMOVE_INVALID_EMAIL, AUTH_SIGN_IN_RESET_INVALID_EMAILS, AUTH_SIGN_OUT, AUTH_SIGN_OUT_ERROR,
    AUTH_SIGN_OUT_SUCCESS
} from "./auth.actions";

export function authReducer(authState: AuthState, action: ActionWPayload) {
    switch (action.type) {

        case AUTH_SET_USER: {
            if (!action.payload) {
                return {...authState, user: null};
            }

            return {
                ...authState,
                user: <FirebaseUserModel>{
                    email: action.payload.email,
                    photoURL: action.payload.photoURL,
                    providerData: action.payload.providerData && action.payload.providerData.length ? action.payload.providerData[0] : null
                }
            };
        }

        case AUTH_SET_USER_ALREADY_LOGGED_IN: {
            return {
                ...authState, userAlreadyLoggedIn: true
            };
        }

        case AUTH_SET_USER_NOT_LOGGED_IN: {
            return {
                ...authState, userAlreadyLoggedIn: false
            };
        }

        case AUTH_RESET_USER: {
            return {...authState, user: null, userAlreadyLoggedIn: false};
        }

        case AUTH_SET_TARGET_STATE: {
            return {...authState, targetState: action.payload};
        }

        case AUTH_RESET_TARGET_STATE: {
            return {...authState, targetState: null};
        }

        case AUTH_SIGN_OUT: {
            return {...authState, signingOut: true};
        }

        case AUTH_SIGN_OUT_SUCCESS:
        case AUTH_SIGN_OUT_ERROR: {
            return {...authState, signingOut: false};
        }


        case AUTH_SIGN_IN_ADD_INVALID_EMAIL: {
            var index = authState.invalidEmails.indexOf(action.payload);
            if (index === -1) {
                return {...authState, invalidEmails: [...authState.invalidEmails, action.payload]};
            }
            break;
        }

        case AUTH_SIGN_IN_RESET_INVALID_EMAILS: {
            return {...authState, invalidEmails: []};
        }

        case AUTH_SIGN_IN_REMOVE_INVALID_EMAIL: {
            var index = authState.invalidEmails.indexOf(action.payload);
            if (index !== -1) {
                return {
                    ...authState, invalidEmails: [
                        ...authState.invalidEmails.slice(0, index),
                        ...authState.invalidEmails.slice(index + 1),
                    ]
                };
            }
            break;
        }

        case AUTH_SIGN_IN_EMAIL_ERROR_WRONG_PASSWORD: {
            return {...authState, invalidPasswordAttempts: authState.invalidPasswordAttempts + 1};
        }
    }

    return {...authState};
}



