import { AppState, EventsState } from "../app.initial.state";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import {
    EVENTS_SET_SELECTED_EVENT, EVENTS_SET_EVENTS, EVENTS_SET_SKIP, EVENTS_SHOW_PAST,
    EVENTS_SHOW_UPCOMING, EVENTS_RESET_EVENTS_STATE, EVENTS_LOADING, EVENTS_NOT_LOADING, EVENTS_SET_UPCOMING_EVENTS,
    EVENTS_SET_PAST_EVENTS
} from "./events.actions";

export function eventsReducer(eventsState: EventsState, action: ActionWPayload) {
    switch (action.type) {
        case EVENTS_LOADING: {
            return {...eventsState, eventsLoading: true};
        }

        case EVENTS_NOT_LOADING: {
            return {...eventsState, eventsLoading: false};
        }

        case EVENTS_RESET_EVENTS_STATE: {
            return {...new EventsState()};
        }

        case EVENTS_SET_EVENTS: {
            return {...eventsState, events: [...action.payload]};
        }

        case EVENTS_SET_UPCOMING_EVENTS: {
            return {...eventsState, upcomingEvents: [...action.payload]};
        }

        case EVENTS_SET_PAST_EVENTS: {
            return {...eventsState, pastEvents: [...action.payload]};
        }

        case EVENTS_SHOW_UPCOMING: {
            return {
                ...eventsState,
                eventsShowUpcoming: true,
                eventsShowPast: false,
                displayedEvents: [...eventsState.upcomingEvents]
            };
        }

        case EVENTS_SHOW_PAST: {
            return {
                ...eventsState,
                eventsShowUpcoming: false,
                eventsShowPast: true,
                displayedEvents: [...eventsState.pastEvents]
            };
        }

        case EVENTS_SET_SKIP: {
            return {...eventsState, eventsSkip: action.payload};
        }

        case EVENTS_SET_SELECTED_EVENT: {
            return {...eventsState, selectedEvent: {...action.payload}};
        }
    }

    return {...eventsState};
}



