import ILocationProvider = angular.ILocationProvider;
import IModule = angular.IModule;

import { StateProvider } from "angular-ui-router";

export function MainConfig($stateProvider: StateProvider) {
    $stateProvider
        .state({
            name: 'main',
            url: '/main',
            component: 'main',
            params: {
                authRequired: true,
            },
            redirectTo: 'main.events'
        })
        .state({
            name: 'main.player',
            url: '/player/:eventId',
            sticky: true,
            params: {
                authRequired: true,
                title: 'Watch',
            },
            views: {
                'player': {
                    component: 'mainPlayer',
                    bindings: {
                        title: 'playerTitle',
                    },
                }
            },
            resolve: {
                playerTitle: () => 'Events Listing',
            },
        })
        .state({
            name: 'main.events',
            url: '/events',
            sticky: true,
            params: {
                authRequired: true,
                title: 'Events'
            },
            views: {
                'events': {
                    component: 'events',
                    bindings: {
                        title: 'eventsTitle',
                    },
                }
            },
            resolve: {
                eventsTitle: () => 'Events Listing',
            },
        })
        .state({
            name: 'main.profile',
            url: '/profile',
            sticky: true,
            params: {
                authRequired: true,
                title: 'Profile'
            },
            views: {
                'profile': {
                    component: 'profile',
                    bindings: {
                        title: 'profileTitle',
                    },
                }
            },
            resolve: {
                profileTitle: () => 'Profile',
            },
        })
        .state({
            name: 'main.settings',
            url: '/settings',
            sticky: true,
            params: {
                authRequired: true,
                title: 'Settings'
            },
            views: {
                'settings': {
                    component: 'settings',
                    bindings: {
                        title: 'settingsTitle',
                    },
                }
            },
            resolve: {
                settingsTitle: () => 'Settings',
            },
        })
}

MainConfig.$inject = ['$stateProvider'];



