import IModule = angular.IModule;

declare var angular: angular.IAngularStatic;

import { MainComponent } from './main.component';
import { MainToolbarComponent } from "./toolbar/main-toolbar.component";
import { MainSidebarComponent } from "./sidebar/main-sidebar.component";

import { MainAnimations } from "./main.animations";
import { MainConfig } from "./main.config";

import { MainTabsComponent } from "./tabs/main-tabs.component";
import { EventsComponent } from "./events/events.component";
import { MainPlayerComponent } from "./player/main-player.component";
import { SettingsComponent } from "./settings/settings.component";
import { ProfileComponent } from "./profile/profile.component";

export let MainModule: IModule = angular.module('main', [
    'ui.router',
    'ngAnimate'
]);

MainModule.config(MainConfig);
MainModule.component('main', MainComponent);
MainModule.component('mainTabs', MainTabsComponent);
MainModule.component('mainToolbar', MainToolbarComponent);
MainModule.component('mainSidebar', MainSidebarComponent);
MainModule.animation('.main__animate', MainAnimations);

MainModule.component('mainPlayer', MainPlayerComponent);
MainModule.component('events', EventsComponent);
MainModule.component('settings', SettingsComponent);
MainModule.component('profile', ProfileComponent);



