import IQService = angular.IQService;
import IQResolveReject = angular.IQResolveReject;
import IDeferred = angular.IDeferred;

import { LogEventModel } from "../../models/log-event.model";
import { DeviceService } from "../pure/device.service";

export class GoogleAnalyticsService {

    constructor(public $q: IQService, public deviceService: DeviceService) {
    }

    initWeb() {
        let deferred = this.$q.defer();

        jQuery.getScript("https://www.google-analytics.com/analytics.js")
            .done((script, textStatus) => {
                this.checkGAIsLoaded(deferred);

                try {
                    this.ga()('create', 'UA-91537408-3', 'auto');
                    this.ga()((tracker) => {
                        deferred.resolve({
                            level: 1,
                            entity: GoogleAnalyticsService,
                            message: 'Google Analytics init successful.'
                        });
                    });
                } catch (err) {
                    deferred.reject({
                        level: 2,
                        entity: GoogleAnalyticsService,
                        message: 'Google Analytics init failed.'
                    });
                }
            })
            .fail((jqxhr, settings, exception) => {
                deferred.reject({
                    level: 2,
                    entity: GoogleAnalyticsService,
                    message: 'Google Analytics could not be loaded.'
                });
            });

        return deferred.promise;
    }

    sendPageViewEvent() {
        let deferred = this.$q.defer();
        this.checkGAIsLoaded(deferred);

        try {
            this.ga()('send', 'pageview');
            deferred.resolve({
                level: 1,
                entity: GoogleAnalyticsService,
                message: "Google Analytics 'pageview' event was sent."
            });
        } catch (err) {
            deferred.reject({
                level: 2,
                entity: GoogleAnalyticsService,
                message: "Google Analytics 'pageview' event failed."
            });
        }

        return deferred.promise;
    }

    sendEvent(logEvent: LogEventModel) {

    }

    ga() {
        if (window['ga']) {
            return window['ga'];
        }
    }

    private checkGAIsLoaded(deferred: IDeferred<LogEventModel>) {
        if (!this.ga()) {
            deferred.reject({
                level: 2,
                entity: GoogleAnalyticsService,
                message: 'ga.js could not be loaded.'
            });
        }
    }
}

GoogleAnalyticsService.$inject = ['$q', 'deviceService'];


