import { Action } from "redux";

import { APP_LOADING, APP_NOT_LOADING, APP_RESET_STATE } from "./app.actions";

export function appResetState(): Action {
    return {
        type: APP_RESET_STATE
    }
}

export function appLoading(): Action {
    return {
        type: APP_LOADING
    }
}

export function appNotLoading(): Action {
    return {
        type: APP_NOT_LOADING
    }
}



