import INgModelController = angular.INgModelController;
import IAttributes = angular.IAttributes;
import IDirective = angular.IDirective;
import IScope = angular.IScope;

import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/throttleTime';

export function OnFocusSelectDirective(): IDirective {
    return {
        restrict: 'A',
        require: '?ngModel',
        link: function NotEqualTo(scope: IScope, elm: JQuery, attr: IAttributes, ctrl: INgModelController) {
            if (!ctrl) return;

            let subscription: Subscription = Observable.fromEvent(elm, 'focus')
                .throttleTime(500)
                .subscribe((e: MouseEvent) => {
                    setTimeout(() => {
                        let value: string = elm.val();
                        if (value && value.length) {
                            try {
                                if (elm['setSelectionRange']) {
                                    elm['setSelectionRange'](0, value.length)
                                } else {
                                    // seems to work on ipad
                                    document.getElementById(elm[0].id)['selectionStart'] = 0;
                                    document.getElementById(elm[0].id)['selectionEnd'] = value.length;
                                }
                            } catch (e) {
                                elm.select();
                            }
                        }
                    }, 100)
                });

            elm.on('$destroy', function () {
                subscription.unsubscribe();
            });
        }
    };
}

OnFocusSelectDirective.$inject = [];


