import IAnimateCallbackObject  = angular.animate.IAnimateCallbackObject;

export let InputValidationAnimation = [(): IAnimateCallbackObject => {
    return {
        // make note that other events (like addClass/removeClass)
        // have different function input parameters
        enter: function (element, doneFn) {
            element.addClass('on-screen-notifications-animations__enter');
            doneFn();
            // remember to call doneFn so that angular
            // knows that the animation has concluded
        },

        move: function (element, doneFn) {
        },

        leave: function (element, doneFn) {
            element.removeClass('on-screen-notifications-animations__enter');
            setTimeout(() => {
                doneFn();
            }, 500);
        }
    };
}];



