import IDirective = angular.IDirective;

import { InputValidationController } from "./input-validation.controller";

import './input-validation.scss';
import './input-validation.animations.scss';

let InputValidationTemplate = require("!!html-loader!./input-validation.template.html");

export function InputValidationDirective(): IDirective {
    return {
        restrict: 'E',
        scope: true,
        transclude: true,
        template: InputValidationTemplate,
        bindToController: true,
        controller: InputValidationController,
        controllerAs: 'vm',
    }
};



