import { ISCEDelegateService, ISCEService } from "angular";
import { StateService, TransitionService } from "ui-router-core";

import { BaseController } from "../../../base.controller";
import { OnScreenNotificationsService } from "../../../services/unpure/on-screen-notifications.service";
import { ContentfulService } from "../../../services/pure/contentful.service";
import { LoggingService } from "../../../services/pure/logging.service";
import { INgRedux } from "ng-redux";
import {
    events_getEventFromContentful,
    events_getEventsFromContentful
} from "../../../redux/events/events.action-creators";

export class MainPlayerController extends BaseController {

    chatEmbedEnabled: boolean = false;

    safePlayerIframeURL: string;
    safeChatEmbedURL: string;

    selectedEvent;

    constructor($scope: angular.IScope,
                $timeout: angular.ITimeoutService,
                $ngRedux: INgRedux,
                $transitions: TransitionService,
                public onScreenNotificationsService: OnScreenNotificationsService,
                public contentfulService: ContentfulService,
                public loggingService: LoggingService,
                public $sce: ISCEService,
                public $element: JQuery) {
        super($scope, $timeout, $ngRedux, $transitions);
    }

    $onInit() {
        this.watchEventId();
    }

    $postLink() {
        this.watchStateAndShowHideOneSignalWidget();
    }

    watchEventId() {
        this.$scope.$watch('state.app.events.selectedEvent', (unsafe_selectedEvent: any) => {
            if (unsafe_selectedEvent && unsafe_selectedEvent.sys && unsafe_selectedEvent.sys.id) {
                // most common state, probably coming from events list
                this.sanitizeEventFieldsAndSetToVm(unsafe_selectedEvent);
                return;
            }

            let routeEventIdParam = this.$ngRedux.getState().router.currentParams['eventId'];
            if (routeEventIdParam && routeEventIdParam.length) {
                // probably after a page refresh or direct link to event
                this.getEventFromContentful(routeEventIdParam);
                return;
            }

            if (!unsafe_selectedEvent) {
                // brand new state, we get the most recent event
                unsafe_selectedEvent = this.getMostRecentEvent();

                if (unsafe_selectedEvent) {
                    this.sanitizeEventFieldsAndSetToVm(unsafe_selectedEvent);
                } else if (this.$ngRedux.getState().router.currentState.name === 'main.player') {
                    this.watchForEventsAndLoadMostRecent();
                }
            }
        }, false);
    }

    watchForEventsAndLoadMostRecent() {
        let unregisterEventsWatcher = this.$scope.$watch('state.app.events.events', (events: any) => {
            if (events) {
                if (events.length === 0) {
                    this.$ngRedux.dispatch(events_getEventsFromContentful(this.$ngRedux.getState().app.events.eventsSkip, 10));
                } else {
                    let unsafe_selectedEvent = this.getMostRecentEvent();
                    if (unsafe_selectedEvent) {
                        this.sanitizeEventFieldsAndSetToVm(unsafe_selectedEvent);
                        unregisterEventsWatcher();
                    }
                }
            }
        }, false);
    }

    sanitizeEventFieldsAndSetToVm(unsafe_selectedEvent) {
        this.selectedEvent = unsafe_selectedEvent;

        let livestreamUrl = this.selectedEvent.fields.url;
        let youTubeUrl = this.selectedEvent.fields.youTubeUrl;
        let uStreamUrl = this.selectedEvent.fields.uStreamUrl;
        let uStreamChatUrl = this.selectedEvent.fields.uStreamChatUrl;

        if (uStreamUrl && uStreamUrl.length) {
            this.validateUStreamURL();

            if (uStreamChatUrl && uStreamChatUrl.length) {
                this.validateUStreamChatURL();
            }
        } else if (livestreamUrl && livestreamUrl.length) {
            this.validateLivestreamURL();
            this.validateSlidoURL();
        } else if (youTubeUrl && youTubeUrl.length) {
            this.validateYouTubeURL();
        }
    }

    getEventFromContentful(eventId) {
        this.$ngRedux.dispatch(events_getEventFromContentful(eventId));
    }

    getMostRecentEvent() {
        let events = this.$ngRedux.getState().app.events.events;
        let upcomingEvents = this.$ngRedux.getState().app.events.upcomingEvents;
        let pastEvents = this.$ngRedux.getState().app.events.pastEvents;

        if (upcomingEvents && upcomingEvents.length) {
            return upcomingEvents[0];
        }

        if (pastEvents && pastEvents.length) {
            return pastEvents[0];
        }

        if (events && events.length) {
            // fallback in case it's not set yet
            upcomingEvents = this.contentfulService.getUpcomingEvents(events);
            pastEvents = this.contentfulService.getUpcomingEvents(events);

            if (upcomingEvents && upcomingEvents.length) {
                return upcomingEvents[0];
            }

            if (pastEvents && pastEvents.length) {
                return pastEvents[0];
            }
        }

        return null;
    }

    validateUStreamChatURL() {
        if (this.selectedEvent.fields.uStreamChatUrl.search(/https:\/\/www\.ustream\.tv\/socialstream/) === 0) {
            this.safeChatEmbedURL = this.$sce.trustAsResourceUrl(this.selectedEvent.fields.uStreamChatUrl);
            this.chatEmbedEnabled = true;
        } else {
            this.loggingService.log({level: 3, message: 'Could not retrieve the event info.'});
        }
    }

    validateUStreamURL() {
        if (this.selectedEvent.fields.uStreamUrl.search(/https:\/\/www\.ustream\.tv\/embed\//) === 0) {
            this.safePlayerIframeURL = this.$sce.trustAsResourceUrl(this.selectedEvent.fields.uStreamUrl);
        } else {
            this.loggingService.log({level: 3, message: 'Could not retrieve the event info.'});
        }
    }

    validateYouTubeURL() {
        if (this.selectedEvent.fields.youTubeUrl.search(/https:\/\/www\.youtube\.com\/embed/) === 0) {
            this.safePlayerIframeURL = this.$sce.trustAsResourceUrl(this.selectedEvent.fields.youTubeUrl);
        } else {
            this.loggingService.log({level: 3, message: 'Could not retrieve the event info.'});
        }
    }

    validateLivestreamURL() {
        if (this.selectedEvent.fields.url.search(/https:\/\/livestream\.com/) === 0) {
            this.safePlayerIframeURL = this.$sce.trustAsResourceUrl(this.selectedEvent.fields.url);
        } else {
            this.loggingService.log({level: 3, message: 'Could not retrieve the event info.'});
        }
    }

    validateSlidoURL() {
        if (this.selectedEvent.fields.slidoId) {
            if (this.selectedEvent.fields.slidoId.search(/[A-Z,a-z,0-9,_]{1,20}/) === 0) {
                this.safeChatEmbedURL = this.$sce.trustAsResourceUrl(`https://app.sli.do/event/${this.selectedEvent.fields.slidoId}/ask`);
                this.chatEmbedEnabled = true;
            } else {

                this.loggingService.log({level: 3, message: 'Could not retrieve the event info.'});
            }
        }
    }

    watchStateAndShowHideOneSignalWidget() {
        this.$scope.$watch('state.router.currentState.name', (state) => {
            if (state === 'main.player') {
                this.hideOneSignalWidget();
            } else {
                this.showOneSignalWidget();
            }
        }, false);
    }

    hideOneSignalWidget() {
        document.querySelector("body").classList.add('hide-one-signal');
    }

    showOneSignalWidget() {
        document.querySelector("body").classList.add('hide-one-signal');
    }
}

MainPlayerController.$inject = [
    ...BaseController.injects,
    'onScreenNotificationsService',
    'contentfulService',
    'loggingService',
    '$sce',
    '$element'
];




