import INgModelController = angular.INgModelController;
import IAttributes = angular.IAttributes;
import IDirective = angular.IDirective;
import IScope = angular.IScope;

export function NotEqualToValidator(): IDirective {
    return {
        restrict: 'A',
        require: '?ngModel',
        link: function NotEqualTo(scope: IScope, elm: JQuery, attr: IAttributes, ctrl: INgModelController) {
            if (!ctrl) return;

            let name = 'notEqualTo';

            if (scope.$eval(attr[name]) == undefined) {
                return;
            }

            attr.$observe(name, function (value: string) {
                ctrl.$validate();
            });

            ctrl.$validators[name] = function (modelValue, viewValue) {
                var attrValue = scope.$eval(attr[name]);
                return !attr[name] || !attrValue || attrValue !== viewValue;
            };
        }
    };
}

NotEqualToValidator.$inject = [];


