import IQService = angular.IQService;
import IDeferred = angular.IDeferred;
import IFilterService = angular.IFilterService;

const _forEach = require("lodash/forEach");
const _filter = require("lodash/filter");
const _groupBy = require("lodash/groupBy");

declare var moment;

// Do not use the node_modules version.. it requires lodash locally... which can be a different version from
// the one the author intended to use. This version is a prod ready version, I added exports to it.
var contentful = require('../../../../node_modules_modified/contentful/contentful.min.js');

export class ContentfulService {

    public readonly dateFormat = 'yyyy-MM-dd';

    constructor(public $q: IQService, public $filter: IFilterService) {
    }

    createClient(options){
        return contentful.createClient(options);
    }

    getEvents(client, skip: number, limit: number) {
        var deferred = this.$q.defer();

        client
            .getEntries({
                'content_type': 'event',
                'include': 1,
                'order': '-fields.date',
                'limit': limit && limit > 0 ? limit : 10,
                'skip': skip && skip > 0 ? skip : 0
            }, true)
            .then(entries => {
                if (entries && entries.items && entries.items.length) {
                    _forEach(entries.items, this.formatEventDate);
                    deferred.resolve(entries.items);
                } else {
                    deferred.reject("No events could be retrieved.");
                }
            })
            .catch(error => {
                deferred.reject(error)
            });

        return deferred.promise;
    }

    getEventBySysId(client, id) {
        var deferred = this.$q.defer();

        client
            .getEntry(id, {
                'include': 1
            })
            .then((event) => {
                if (event) {
                    deferred.resolve(this.formatEventDate(event));
                } else {
                    deferred.reject("Event could not be retrieved.");
                }
            })
            .catch((err) => {
                deferred.reject(err)
            });

        return deferred.promise;
    }

    formatEventDate(event) {
        event.timestamp = new Date(event.fields.date).getTime();
        event.fields.formattedDate = moment(event.timestamp).fromNow();
        event.playEvent = false;
        return event;
    }

    getUpcomingEvents(contentfulEvents) {
        var upcomingEvents = _filter(contentfulEvents, (e: any) => {
            if (moment(e.timestamp).isAfter(moment().subtract(8, 'hour'))) {
                return e;
            }
        });
        return this.$filter('orderBy')(upcomingEvents, 'timestamp', false);
    }

    getPastEvents(contentfulEvents) {
        var pastEvents = _filter(contentfulEvents, (e: any) => {
            if (moment(e.timestamp).isBefore(moment().subtract(8, 'hour'))) {
                return e;
            }
        });
        return this.$filter('orderBy')(pastEvents, 'timestamp', true);
    }

    groupEventsByDay(events) {
        return _groupBy(events, (e: any) => {
            return this.$filter('date')(e.timestamp, this.dateFormat);
        });
    }

    getContentfulGlobal() {
        if (contentful) {
            return contentful;
        }
    }
}

ContentfulService.$inject = ['$q', '$filter'];



