import IScope = angular.IScope;

import { INgRedux } from "ng-redux";
import { TransitionService } from "ui-router-core";

import { BaseController } from "../../../base.controller";

import { auth_signOut } from "../../../redux/auth/auth.action-creators";

const packageJson = require('../../../../../package.json');

export class MainSidebarController extends BaseController {

    version: string = packageJson.version;

    constructor($scope: angular.IScope,
                $timeout: angular.ITimeoutService,
                $ngRedux: INgRedux,
                $transitions: TransitionService) {
        super($scope, $timeout, $ngRedux, $transitions);
    }

    signOut() {
        this.$ngRedux.dispatch(auth_signOut());
    }

    selectPlayerTab() {
        let selectedEvent = this.$ngRedux.getState().app.events.selectedEvent;
        this.$scope.stateGo('main.player', {
            eventId: selectedEvent && selectedEvent.sys.id ? selectedEvent.sys.id : ''
        })
    }
}

MainSidebarController.$inject = [...BaseController.injects];





