import INgModelController = angular.INgModelController;
import IAttributes = angular.IAttributes;
import IDirective = angular.IDirective;
import IScope = angular.IScope;

import { FirebaseService } from "../../../services/pure/firebase.service";

export function ValidateEmailValidator(firebaseService: FirebaseService): IDirective {
    return {
        restrict: 'A',
        require: '?ngModel',
        link: function ValidateUserValidatorDirectiveLink(scope: IScope, elm: JQuery, attr: IAttributes, ctrl: INgModelController) {
            if (!ctrl) return;

            let name = 'validateEmail';

            attr.$observe(name, function (invalidEmails: string[]) {
                ctrl.$validate();
            });

            ctrl.$validators[name] = function (modelValue, viewValue) {
                var attrValue = scope.$eval(attr[name]);
                return !attr[name] || !attrValue || !attrValue.length || attrValue.indexOf(viewValue) === -1;
            };
        }
    };
}


ValidateEmailValidator.$inject = ['firebaseService'];


