import ILocationProvider = angular.ILocationProvider;
import IModule = angular.IModule;

import { StateProvider, StateService, TransitionService, Transition } from "angular-ui-router";

export function AuthConfig($stateProvider: StateProvider) {
    $stateProvider
        .state('login', {
            url: '/auth/login?error',
            params: {
                authRequired: false,
                hideAuthNavigation: true,
            },
            component: 'login'
        })
        .state('loginEmail', {
            url: '/auth/login-email',
            params: {
                authRequired: false,
                hideAuthNavigation: false,
                title: 'Sign In',
                backState: 'login',
            },
            component: 'loginEmail'
        })
        .state('resetPassword', {
            url: '/auth/reset',
            component: 'resetPassword',
            params: {
                authRequired: false,
                email: '',
                title: 'Reset Password',
                backState: 'login',
                hideAuthNavigation: false,
            }
        })
        .state('signup', {
            url: '/auth/signup',
            component: 'signUp',
            params: {
                authRequired: false,
                email: '',
                title: 'Sign Up',
                backState: 'login',
                hideAuthNavigation: false,
            }
        });
}

AuthConfig.$inject = ['$stateProvider'];



