import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import { DeviceState } from "../app.initial.state";
import {
    DEVICE_SET_BADGE_NUMBER, DEVICE_SET_DEVICE_TOKEN, DEVICE_SET_DEVICE_TYPE,
    DEVICE_SET_PLATFORM
} from "./device.actions";

export function deviceReducer(deviceState: DeviceState, action: ActionWPayload) {
    switch (action.type) {

        case DEVICE_SET_BADGE_NUMBER: {
            return {...deviceState, badgeNumber: action.payload};
        }

        case DEVICE_SET_DEVICE_TOKEN: {
            return {...deviceState, deviceToken: action.payload};
        }

        case DEVICE_SET_DEVICE_TYPE: {
            return {...deviceState, deviceType: action.payload};
        }

        case DEVICE_SET_PLATFORM: {
            return {...deviceState, platform: action.payload};
        }
    }

    return {...deviceState};
}



