import { injector } from "../../app";
import { LoggingService } from "../../services/pure/logging.service";
import { LogEventModel } from "../../models/log-event.model";

import {
    AUTH_RESET_PASSWORD, AUTH_RESET_PASSWORD_ERROR, AUTH_RESET_PASSWORD_SUCCESS,
    AUTH_SIGN_IN_EMAIL_ERROR_INVALID_EMAIL, AUTH_SIGN_IN_EMAIL_ERROR_WRONG_PASSWORD, AUTH_SIGN_IN_EMAIL_OTHER_ERROR,
    AUTH_SIGN_IN_EMAIL_SUCCESS, AUTH_SIGN_IN_FACEBOOK, AUTH_SIGN_IN_FACEBOOK_ERROR, AUTH_SIGN_IN_FACEBOOK_SUCCESS,
    AUTH_SIGN_IN_FACEBOOK_SUCCESS_CORDOVA,
    AUTH_SIGN_IN_GOOGLE_LOGIN_ERROR,
    AUTH_SIGN_IN_GOOGLE_LOGIN_SUCCESS, AUTH_SIGN_OUT, AUTH_SIGN_OUT_SUCCESS, AUTH_SIGN_UP_EMAIL_ERROR,
    AUTH_SIGN_UP_EMAIL_SUCCESS,
    AUTH_SIGN_IN_FACEBOOK_ERROR_CORDOVA, AUTH_SIGN_IN_FACEBOOK_CANCELLED_CORDOVA,
    AUTH_SIGN_IN_GOOGLE_LOGIN_SUCCESS_CORDOVA, AUTH_SIGN_IN_GOOGLE_LOGIN_ERROR_CORDOVA,
} from "../auth/auth.actions";
import {
    FIREBASE_INIT_WEB_ERROR, FIREBASE_INIT_WEB_SUCCESS, FIREBASE_LOAD_SCRIPT_ERROR,
    FIREBASE_LOAD_SCRIPT_SUCCESS, FIREBASE_INIT_CORDOVA_ERROR, FIREBASE_INIT_CORDOVA_SUCCESS
} from "../firebase/firebase.actions";
import {
    GOOGLE_LOGIN_CANCELLED,
    GOOGLE_LOGIN_ERROR,
    GOOGLE_LOGIN_INIT_WEB_ERROR
} from "../google-login/google-login.actions";
import {
    FACEBOOK_INIT_WEB_ERROR,
    FACEBOOK_INIT_WEB_SUCCESS, FACEBOOK_LOAD_SCRIPT_ERROR,
    FACEBOOK_LOAD_SCRIPT_SUCCESS,
    FACEBOOK_INIT_CORDOVA_ERROR, FACEBOOK_INIT_CORDOVA_SUCCESS,
} from "../facebook/facebook.actions";
import { ILogEventOptions } from "../../interfaces/log-event-option.interface";
import { CONTENTFUL_INIT_WEB_ERROR } from "../contentful/contentful.actions";
import {
    EVENTS_GET_EVENTS_CONTENTFUL_ERROR,
    EVENTS_GET_EVENTS_CONTENTFUL_SUCCESS,
    EVENTS_GET_EVENTS_CONTENTFUL, EVENTS_GET_EVENT_CONTENTFUL_ERROR
} from "../events/events.actions";

export let loggingListeners = {
    [AUTH_SIGN_OUT]: (action, dispatch, state) => {
        loggerHelper({
            level: 0,
            action: action,
            message: 'Signing out...',
        })
    },
    [AUTH_SIGN_OUT_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            action: action,
            message: 'Sign out successful !',
            displayOnScreen: true,
            dismissAfter: 2000
        })
    },
    [AUTH_RESET_PASSWORD_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 2,
            action: action,
            message: action.payload.message ? action.payload.message : 'Error while resetting password.',
        })
    },
    [AUTH_RESET_PASSWORD_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            message: 'Ok done! Check your inbox.',
            autoDismiss: false,
            displayOnScreen: true,
        })
    },
    [AUTH_SIGN_UP_EMAIL_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            message: 'Account created!',
            displayOnScreen: true,
        })
    },
    [AUTH_SIGN_UP_EMAIL_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 2,
            action: action,
            message: action.payload.message ? action.payload.message : 'Error while creating account.',
        })
    },
    [AUTH_SIGN_IN_EMAIL_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            action: action,
            message: 'Sign In Successful',
            displayOnScreen: true,
        })
    },
    [AUTH_SIGN_IN_EMAIL_ERROR_INVALID_EMAIL]: (action, dispatch, state) => {
        loggerHelper({
            level: 2,
            action: action,
            message: 'Email is not registered.',
        })
    },
    [AUTH_SIGN_IN_EMAIL_ERROR_WRONG_PASSWORD]: (action, dispatch, state) => {
        loggerHelper({
            level: 2,
            action: action,
            message: 'Invalid password.',
        })
    },
    [AUTH_SIGN_IN_EMAIL_OTHER_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: action.payload.message,
            displayOnScreen: true,
        })
    },
    [AUTH_SIGN_IN_FACEBOOK_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            action: action,
            message: 'Sign In Successful (with Facebook).',
            displayOnScreen: true,
        })
    },
    [AUTH_SIGN_IN_FACEBOOK_SUCCESS_CORDOVA]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            action: action,
            message: 'Sign In Successful (with Facebook).',
            displayOnScreen: true,
        })
    },
    [AUTH_SIGN_IN_FACEBOOK_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: 'Error occurred with Facebook login.',
            displayOnScreen: true,
        })
    },
    [AUTH_SIGN_IN_FACEBOOK_ERROR_CORDOVA]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: 'Error occurred with Facebook login.',
            displayOnScreen: true,
        })
    },
    [AUTH_SIGN_IN_FACEBOOK_CANCELLED_CORDOVA]: (action, dispatch, state) => {
        loggerHelper({
            level: 2,
            action: action,
            message: 'Facebook login was probably cancelled.',
            displayOnScreen: true,
        })
    },
    [AUTH_SIGN_IN_GOOGLE_LOGIN_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            action: action,
            message: 'Sign In Successful (with Google).',
            displayOnScreen: true,
        })
    },
    [AUTH_SIGN_IN_GOOGLE_LOGIN_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: 'Error occurred with Google login.',
            displayOnScreen: true,
        })
    },
    [AUTH_SIGN_IN_GOOGLE_LOGIN_SUCCESS_CORDOVA]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            action: action,
            message: 'Sign In Successful (with Google).',
            displayOnScreen: true,
        })
    },
    [AUTH_SIGN_IN_GOOGLE_LOGIN_ERROR_CORDOVA]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: 'Error occurred with Google login.',
            displayOnScreen: true,
        })
    },

    [FIREBASE_LOAD_SCRIPT_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            action: action,
            message: 'Firebase script was successfully loaded.'
        })
    },
    [FIREBASE_LOAD_SCRIPT_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: 'Firebase script could not be loaded.'
        })
    },
    [FIREBASE_INIT_WEB_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            action: action,
            message: 'Firebase init successful.',
        })
    },
    [FIREBASE_INIT_WEB_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: 'Firebase init failed.',
            displayOnScreen: true
        })
    },
    [FIREBASE_INIT_CORDOVA_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            action: action,
            message: 'Firebase init successful (Cordova).',
        })
    },
    [FIREBASE_INIT_CORDOVA_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: 'Firebase init failed (Cordova).',
            displayOnScreen: true
        })
    },
    

    [FACEBOOK_LOAD_SCRIPT_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            action: action,
            message: 'Facebook script was successfully loaded.'
        })
    },
    [FACEBOOK_LOAD_SCRIPT_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: 'Facebook script could not be loaded.'
        })
    },
    [FACEBOOK_INIT_WEB_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            action: action,
            message: 'Facebook SDK init successful.',
        })
    },
    [FACEBOOK_INIT_WEB_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: 'Facebook SDK init failed.'
        })
    },
    [FACEBOOK_INIT_CORDOVA_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            action: action,
            message: 'Facebook SDK init successful.',
        })
    },
    [FACEBOOK_INIT_CORDOVA_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: 'Facebook SDK init failed.'
        })
    },


    [GOOGLE_LOGIN_INIT_WEB_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: 'Google Login init failed.',
        })
    },
    [GOOGLE_LOGIN_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 2,
            action: action,
            message: 'Error occurred with Google login.',
            displayOnScreen: true
        })
    },
    [GOOGLE_LOGIN_CANCELLED]: (action, dispatch, state) => {
        loggerHelper({
            level: 2,
            message: 'Google login was cancelled.',
            displayOnScreen: true
        })
    },

    [CONTENTFUL_INIT_WEB_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: 'Contentful init failed.',
        })
    },

    [EVENTS_GET_EVENTS_CONTENTFUL]: (action, dispatch, state) => {
        loggerHelper({
            level: 0,
            message: 'Retrieving events.',
        })
    },
    [EVENTS_GET_EVENTS_CONTENTFUL_SUCCESS]: (action, dispatch, state) => {
        loggerHelper({
            level: 1,
            message: 'Events list is up to date.',
        })
    },
    [EVENTS_GET_EVENTS_CONTENTFUL_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: action.payload.message,
            displayOnScreen: true
        })
    },
    [EVENTS_GET_EVENT_CONTENTFUL_ERROR]: (action, dispatch, state) => {
        loggerHelper({
            level: 3,
            action: action,
            message: action.payload.message,
            displayOnScreen: true
        })
    },
};

function loggerHelper(log: ILogEventOptions | LogEventModel) {
    let loggingService = <LoggingService>injector.get('loggingService');
    loggingService.log(log)
}


