import { Dispatch } from "redux";

import { injector } from "../../app";

import { IListenerMiddleWareListener } from "../app-listener.middleware";
import { FacebookService } from "../../services/pure/facebook.service";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import { IGlobalAppState } from "../../interfaces/global-app-state.interface";
import { FacebookAuthResponse } from "../../interfaces/facebook-auth-response.interface";

import { FACEBOOK_APP_IDS } from "../../app.constants";
import {
    FACEBOOK_INIT_CORDOVA, FACEBOOK_INIT_WEB, FACEBOOK_SIGN_IN_OAUTH,
    FACEBOOK_SIGN_IN_OAUTH_CORDOVA
} from "./facebook.actions";

import { appLoading } from "../app.action-creators";
import {
    facebookDisableLogin, facebookEnableLogin, facebookInitWebError,
    facebookLoading,
    facebookLoadScriptError, facebookLoadScriptSuccess, facebookNotLoading, facebookGlobalReady,
    facebookSignInOAuthSuccess, facebookSignInOAuthSuccessCordova, facebookInitErrorCordova, facebookInitSuccessCordova,
    facebookCordovaGlobalReady
} from "./facebook.action-creators";
import { auth_signInFacebookWToken, auth_signInFacebookWTokenCordova } from "../auth/auth.action-creators";

export let facebookListener: IListenerMiddleWareListener = {
    [FACEBOOK_INIT_WEB]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let facebookService = <FacebookService>injector.get('facebookService');

        dispatch(facebookLoading());
        dispatch(facebookDisableLogin());

        facebookService.jQueryLoadFacebookSDKScript()
            .done((script, textStatus) => {
                try {
                    if (!facebookService.getFBGlobal()) {
                        dispatch(facebookLoadScriptError());
                        dispatch(facebookNotLoading());
                        return;
                    }

                    dispatch(facebookGlobalReady());
                    dispatch(facebookLoadScriptSuccess());
                    dispatch(facebookNotLoading());
                    dispatch(facebookEnableLogin());

                    facebookService.getFBGlobal().init(FACEBOOK_APP_IDS);

                } catch (error) {
                    dispatch(facebookInitWebError(error));
                    dispatch(facebookNotLoading());
                }
            })
            .fail((jqxhr, settings, exception) => {
                dispatch(facebookLoadScriptError());
                dispatch(facebookNotLoading());
            });
    },
    [FACEBOOK_SIGN_IN_OAUTH]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let facebookService = <FacebookService>injector.get('facebookService');

        try {
            dispatch(facebookLoading());
            dispatch(facebookDisableLogin());

            facebookService.getLoginStatus((response: FacebookAuthResponse) => {
                dispatch(appLoading());

                if (response.status === 'connected') {
                    dispatch(facebookSignInOAuthSuccess());
                    dispatch(auth_signInFacebookWToken(response));
                }

                dispatch(facebookEnableLogin());
                dispatch(facebookNotLoading());
            });
        } catch (error) {
            dispatch(facebookEnableLogin());
            dispatch(facebookNotLoading());
        }
    },
    [FACEBOOK_INIT_CORDOVA]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let facebookService = <FacebookService>injector.get('facebookService');

        dispatch(facebookLoading());
        dispatch(facebookDisableLogin());

        try {
            if (facebookService.getFBPluginGlobal()) {
                dispatch(facebookCordovaGlobalReady());
                dispatch(facebookInitSuccessCordova());
                dispatch(facebookEnableLogin());
                dispatch(facebookNotLoading());
            } else {
                throw new Error('Facebook plugin not available!');
            }

        } catch (error) {
            dispatch(facebookInitErrorCordova(error));
            dispatch(facebookNotLoading());
        }
    },
    [FACEBOOK_SIGN_IN_OAUTH_CORDOVA]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let facebookService = <FacebookService>injector.get('facebookService');

        try {
            dispatch(facebookLoading());
            dispatch(facebookDisableLogin());

            facebookService.getLoginStatusCordova((response: FacebookAuthResponse) => {
                dispatch(appLoading());

                if (response.status === 'connected') {
                    dispatch(facebookSignInOAuthSuccessCordova());
                    dispatch(auth_signInFacebookWTokenCordova(response));
                }

                dispatch(facebookEnableLogin());
                dispatch(facebookNotLoading());
            });
        } catch (error) {
            dispatch(facebookEnableLogin());
            dispatch(facebookNotLoading());
        }
    }
};


