import { GoogleAnalyticsState } from "../app.initial.state";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import { GOOGLE_ANALYTICS_LOADING, GOOGLE_ANALYTICS_NOT_LOADING } from "./google-analytics.actions";

export function googleAnalyticsReducer(googleAnalyticsState: GoogleAnalyticsState, action: ActionWPayload) {
    switch (action.type) {
        case GOOGLE_ANALYTICS_LOADING: {
            return {...googleAnalyticsState, googleAnalyticsLoading: true};
        }
        case GOOGLE_ANALYTICS_NOT_LOADING: {
            return {...googleAnalyticsState, googleAnalyticsLoading: false};
        }
    }

    return {...googleAnalyticsState};
}



