import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import { FacebookState } from "../app.initial.state";
import {
    FACEBOOK_DISABLE_LOGIN,
    FACEBOOK_LOADING,
    FACEBOOK_NOT_LOADING,
    FACEBOOK_ENABLE_LOGIN, FACEBOOK_GLOBAL_READY, FACEBOOK_GLOBAL_READY_CORDOVA
} from "./facebook.actions";

export function facebookReducer(facebookState: FacebookState, action: ActionWPayload) {
    switch (action.type) {

        case FACEBOOK_LOADING: {
            return {...facebookState, facebookLoading: true};
        }

        case FACEBOOK_NOT_LOADING: {
            return {...facebookState, facebookLoading: false};
        }

        case FACEBOOK_DISABLE_LOGIN: {
            return {...facebookState, facebookLoginDisabled: true};
        }

        case FACEBOOK_ENABLE_LOGIN: {
            return {...facebookState, facebookLoginDisabled: false};
        }

        case FACEBOOK_GLOBAL_READY: {
            return {...facebookState, facebook: true};
        }

        case FACEBOOK_GLOBAL_READY_CORDOVA: {
            return {...facebookState, facebookCordovaPlugin: true};
        }
    }

    return {...facebookState};
}



