import { GoogleLoginState } from "../app.initial.state";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import {
    GOOGLE_LOGIN_DISABLE, GOOGLE_LOGIN_ENABLE, GOOGLE_LOGIN_LOADING,
    GOOGLE_LOGIN_NOT_LOADING, GOOGLE_LOGIN_AUTH2_READY, GOOGLE_LOGIN_GLOBAL_READY, GOOGLE_LOGIN_PLUGIN_READY
} from "./google-login.actions";

export function googleLoginReducer(googleLoginState: GoogleLoginState, action: ActionWPayload) {
    switch (action.type) {
        case GOOGLE_LOGIN_LOADING: {
            return {...googleLoginState, googleLoginLoading: true};
        }

        case GOOGLE_LOGIN_NOT_LOADING: {
            return {...googleLoginState, googleLoginLoading: false};
        }

        case GOOGLE_LOGIN_ENABLE: {
            return {...googleLoginState, googleLoginDisabled: false};
        }

        case GOOGLE_LOGIN_GLOBAL_READY: {
            return {...googleLoginState, gapiReady: true};
        }

        case GOOGLE_LOGIN_PLUGIN_READY: {
            return {...googleLoginState, pluginReady: true};
        }

        case GOOGLE_LOGIN_AUTH2_READY: {
            return {...googleLoginState, auth2Ready: true};
        }
    }

    return {...googleLoginState};
}



