import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import { ContentfulState } from "../app.initial.state";
import {
    CONTENTFUL_LOADING, CONTENTFUL_NOT_LOADING, CONTENTFUL_RESET_CLIENT,
    CONTENTFUL_SET_CLIENT
} from "./contentful.actions";

export function contentfulReducer(contentfulState: ContentfulState, action: ActionWPayload) {
    switch (action.type) {
        case CONTENTFUL_LOADING: {
            return {...contentfulState, contentfulLoading: true};
        }

        case CONTENTFUL_NOT_LOADING: {
            return {...contentfulState, contentfulLoading: false};
        }

        case CONTENTFUL_SET_CLIENT: {
            return {...contentfulState, contentfulClient: action.payload};
        }

        case CONTENTFUL_RESET_CLIENT: {
            return {...contentfulState, contentfulClient: null};
        }
    }

    return {...contentfulState};
}



