import { Action } from "redux";
import {
    GOOGLE_LOGIN_LOADING,
    GOOGLE_LOGIN_NOT_LOADING,
    GOOGLE_LOGIN_ENABLE,
    GOOGLE_LOGIN_DISABLE, GOOGLE_LOGIN_OAUTH_SUCCESS, GOOGLE_LOGIN_ERROR, GOOGLE_LOGIN_CANCELLED, GOOGLE_LOGIN_INIT_WEB,
    GOOGLE_LOGIN_INIT_WEB_SUCCESS, GOOGLE_LOGIN_INIT_WEB_ERROR, GOOGLE_LOGIN_LOAD_SCRIPT_SUCCESS,
    GOOGLE_LOGIN_LOAD_SCRIPT_ERROR, GOOGLE_LOGIN_GLOBAL_READY, GOOGLE_LOGIN_AUTH2_READY, GOOGLE_LOGIN_OAUTH,
    GOOGLE_LOGIN_INIT_CORDOVA_ERROR, GOOGLE_LOGIN_INIT_CORDOVA_SUCCESS, GOOGLE_LOGIN_INIT_CORDOVA,
    GOOGLE_LOGIN_PLUGIN_READY
} from "./google-login.actions";

import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";

export function googleLoginLoading(): Action {
    return {
        type: GOOGLE_LOGIN_LOADING
    }
}

export function googleLoginNotLoading(): Action {
    return {
        type: GOOGLE_LOGIN_NOT_LOADING
    }
}

export function googleLoginEnable(): Action {
    return {
        type: GOOGLE_LOGIN_ENABLE
    }
}

export function googleLoginDisable(): Action {
    return {
        type: GOOGLE_LOGIN_DISABLE
    }
}

export function googleLoginSuccess(): Action {
    return {
        type: GOOGLE_LOGIN_OAUTH_SUCCESS
    }
}

export function googleLoginOAuth(): Action {
    return {
        type: GOOGLE_LOGIN_OAUTH
    }
}

export function googleLoginOAuthSuccess(): Action {
    return {
        type: GOOGLE_LOGIN_OAUTH_SUCCESS
    }
}

export function googleLoginCancelled(): Action {
    return {
        type: GOOGLE_LOGIN_CANCELLED
    }
}

export function googleLoginError(error): ActionWPayload {
    return {
        type: GOOGLE_LOGIN_ERROR,
        payload: error
    }
}

export function googleLoginInitWeb(): Action {
    return {
        type: GOOGLE_LOGIN_INIT_WEB
    }
}

export function googleLoginLoadScriptSuccess(): Action {
    return {
        type: GOOGLE_LOGIN_LOAD_SCRIPT_SUCCESS
    }
}

export function googleLoginLoadScriptError(error): ActionWPayload {
    return {
        type: GOOGLE_LOGIN_LOAD_SCRIPT_ERROR,
        payload: error
    }
}

export function googleLoginInitWebSuccess(): Action {
    return {
        type: GOOGLE_LOGIN_INIT_WEB_SUCCESS
    }
}

export function googleLoginInitWebError(error): ActionWPayload {
    return {
        type: GOOGLE_LOGIN_INIT_WEB_ERROR,
        payload: error
    }
}

export function googleLoginInitCordova(): Action {
    return {
        type: GOOGLE_LOGIN_INIT_CORDOVA
    }
}

export function googleLoginInitCordovaSuccess(): Action {
    return {
        type: GOOGLE_LOGIN_INIT_CORDOVA_SUCCESS
    }
}

export function googleLoginInitCordovaError(error): ActionWPayload {
    return {
        type: GOOGLE_LOGIN_INIT_CORDOVA_ERROR,
        payload: error
    }
}

export function googleLoginPluginReady(): Action {
    return {
        type: GOOGLE_LOGIN_PLUGIN_READY
    }
}

export function googleLoginGlobalReady(): Action {
    return {
        type: GOOGLE_LOGIN_GLOBAL_READY
    }
}

export function googleLoginAuth2Ready(): Action {
    return {
        type: GOOGLE_LOGIN_AUTH2_READY
    }
}


