import { Dispatch } from "redux";

import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import { injector } from "../../app";
import { IGlobalAppState } from "../../interfaces/global-app-state.interface";
import { FirebaseService } from "../../services/pure/firebase.service";
import { IListenerMiddleWareListener } from "../app-listener.middleware";

import { FIREBASE_INIT_WEB, FIREBASE_INIT_CORDOVA } from "./firebase.actions";

import { auth_resetUser, auth_setUser, auth_setUserAlreadyLoggedIn } from "../auth/auth.action-creators";
import {
    firebaseInitWebError,
    firebaseInitWebSuccess,
    firebaseLoading,
    firebaseLoadScriptError,
    firebaseLoadScriptSuccess,
    firebaseNotLoading,
    firebaseGlobalReady, firebaseInitCordovaSuccess, firebaseInitCordovaError
} from "./firebase.action-creators";

export let firebaseListener: IListenerMiddleWareListener = {
    [FIREBASE_INIT_WEB]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let firebaseService = <FirebaseService>injector.get('firebaseService');

        dispatch(firebaseLoading());

        firebaseService.jQueryLoadFirebaseScript()
            .done((script, textStatus) => {
                try {
                    if (!firebaseService.getFirebaseGlobal()) {
                        throw new Error("Firebase not loaded.");
                    }

                    dispatch(firebaseLoadScriptSuccess());

                    firebaseService.initWeb(); // this is synchronous

                    dispatch(firebaseInitWebSuccess());
                    dispatch(firebaseGlobalReady());
                    dispatch(firebaseNotLoading());

                    let onAuthStateChangedUnsubscribe = firebaseService.getFirebaseGlobal()
                        .auth()
                        .onAuthStateChanged((user) => {
                            if (user) {
                                dispatch(auth_setUser(user));
                                dispatch(auth_setUserAlreadyLoggedIn());
                            } else {
                                dispatch(auth_resetUser());
                            }
                            onAuthStateChangedUnsubscribe();
                        })

                } catch (error) {
                    dispatch(firebaseInitWebError(error));
                    dispatch(auth_resetUser());
                    dispatch(firebaseNotLoading());
                }
            })
            .fail((jqxhr, settings, exception) => {
                dispatch(firebaseLoadScriptError(exception));
                dispatch(firebaseNotLoading());
            });
    },
    [FIREBASE_INIT_CORDOVA]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let firebaseService = <FirebaseService>injector.get('firebaseService');

        dispatch(firebaseLoading());

        try {
            if (!firebaseService.getFirebaseGlobal()) {
                throw new Error("Firebase not loaded.");
            }

            if (!firebaseService.getFirebasePluginGlobal()) {
                throw new Error("Firebase plugin not loaded.");
            }

            firebaseService.initCordova(); // this is synchronous

            dispatch(firebaseInitCordovaSuccess());
            dispatch(firebaseGlobalReady());
            dispatch(firebaseNotLoading());

            let onAuthStateChangedUnsubscribe = firebaseService.getFirebaseGlobal()
                .auth()
                .onAuthStateChanged((user) => {
                    if (user) {
                        dispatch(auth_setUser(user));
                        dispatch(auth_setUserAlreadyLoggedIn());
                    } else {
                        dispatch(auth_resetUser());
                    }
                    onAuthStateChangedUnsubscribe();
                })

        } catch (error) {
            dispatch(firebaseInitCordovaError(error));
            dispatch(auth_resetUser());
            dispatch(firebaseNotLoading());
        }
    }
};


