import { Action } from "redux";
import {
    FACEBOOK_DISABLE_LOGIN, FACEBOOK_ENABLE_LOGIN, FACEBOOK_LOADING,
    FACEBOOK_NOT_LOADING, FACEBOOK_INIT_WEB, FACEBOOK_LOAD_SCRIPT_SUCCESS, FACEBOOK_LOAD_SCRIPT_ERROR,
    FACEBOOK_INIT_WEB_ERROR, FACEBOOK_INIT_WEB_SUCCESS, FACEBOOK_GLOBAL_READY, FACEBOOK_SIGN_IN_OAUTH,
    FACEBOOK_SIGN_IN_OAUTH_SUCCESS, FACEBOOK_SIGN_IN_OAUTH_CORDOVA, FACEBOOK_SIGN_IN_OAUTH_SUCCESS_CORDOVA,
    FACEBOOK_INIT_CORDOVA, FACEBOOK_INIT_CORDOVA_ERROR
}
    from
        "./facebook.actions";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import { FACEBOOK_INIT_CORDOVA_SUCCESS } from "./facebook.actions";

export function facebookLoading(): Action {
    return {
        type: FACEBOOK_LOADING
    }
}
export function facebookNotLoading(): Action {
    return {
        type: FACEBOOK_NOT_LOADING
    }
}
export function facebookDisableLogin(): Action {
    return {
        type: FACEBOOK_DISABLE_LOGIN
    }
}

export function facebookEnableLogin(): Action {
    return {
        type: FACEBOOK_ENABLE_LOGIN
    }
}

export function facebookInitWebError(error): ActionWPayload {
    return {
        type: FACEBOOK_INIT_WEB_ERROR,
        payload: error
    }
}

export function facebookInitWebSuccess(): Action {
    return {
        type: FACEBOOK_INIT_WEB_SUCCESS
    }
}

export function facebookInitWeb(): Action {
    return {
        type: FACEBOOK_INIT_WEB
    }
}

export function facebookLoadScriptSuccess(): Action {
    return {
        type: FACEBOOK_LOAD_SCRIPT_SUCCESS
    }
}

export function facebookLoadScriptError(): Action {
    return {
        type: FACEBOOK_LOAD_SCRIPT_ERROR
    }
}

export function facebookInitCordova(): Action {
    return {
        type: FACEBOOK_INIT_CORDOVA
    }
}

export function facebookInitSuccessCordova(): Action {
    return {
        type: FACEBOOK_INIT_CORDOVA_SUCCESS
    }
}

export function facebookInitErrorCordova(error): ActionWPayload {
    return {
        type: FACEBOOK_INIT_CORDOVA_ERROR,
        payload: error
    }
}

export function facebookSignInOAuth(): Action {
    return {
        type: FACEBOOK_SIGN_IN_OAUTH
    }
}

export function facebookSignInOAuthSuccess(): Action {
    return {
        type: FACEBOOK_SIGN_IN_OAUTH_SUCCESS
    }
}

export function facebookGlobalReady(): Action {
    return {
        type: FACEBOOK_GLOBAL_READY
    }
}

export function facebookCordovaGlobalReady(): Action {
    return {
        type: FACEBOOK_GLOBAL_READY
    }
}

export function facebookSignInOAuthCordova(): Action {
    return {
        type: FACEBOOK_SIGN_IN_OAUTH_CORDOVA
    }
}

export function facebookSignInOAuthSuccessCordova(): Action {
    return {
        type: FACEBOOK_SIGN_IN_OAUTH_SUCCESS_CORDOVA
    }
}




