import { StateService, TransitionService } from "ui-router-core";

import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/observable/timer';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/takeWhile';
import 'rxjs/add/operator/repeat';

const _remove = require('lodash/remove');

import { OnScreenNotificationsService } from "../../../services/unpure/on-screen-notifications.service";
import { OnScreenNotificationModel } from "../../../models/on-screen-notification.model";
import { BaseController } from "../../../base.controller";
import { INgRedux } from "ng-redux";

export class OnScreenNotificationsController extends BaseController {

    notifications: OnScreenNotificationModel[] = [];
    deletionSub: Subscription;

    constructor($scope: angular.IScope,
                $timeout: angular.ITimeoutService,
                $ngRedux: INgRedux,
                $transitions: TransitionService,
                public onScreenNotificationsService: OnScreenNotificationsService) {
        super($scope, $timeout, $ngRedux, $transitions);
    }

    $postLink() {
        this.subscribeToService();
    }

    private subscribeToService() {
        this.onScreenNotificationsService.onScreenNotificationsQueue$
            .subscribe((notification: OnScreenNotificationModel) => {
                // comes from redux listener logging.listeners, need to trigger digest
                this.$scope.$applyAsync(() => {
                        this.notifications.push(notification);
                        if (notification.autoDismiss) {
                            this.scheduleAutohide(notification);
                        }
                    }
                );
            });

        // only delete one message at a time
        this.onScreenNotificationsService
            .onScreenNotificationsToBeDeletedQueue$
            .subscribe((message: OnScreenNotificationModel) => {
                this.onScreenNotificationsService.onScreenNotificationssToBeDeleted.push(message);
                if (this.onScreenNotificationsService.onScreenNotificationssToBeDeleted.length && this.deletionSub.closed) {
                    // re-schedule deletion if it was closed
                    this.deletionSub = this.subscribeForDeletion();
                }
            });

        // schedule deletion
        this.deletionSub = this.subscribeForDeletion();
    }

    private subscribeForDeletion() {
        var timer$ = new Subject();
        return Observable.timer(0, 1500)
            .map(i => {
                timer$.closed = !this.onScreenNotificationsService.onScreenNotificationssToBeDeleted.length;
            })
            .takeWhile(i => {
                return !timer$.closed
            })
            .subscribe(timer => {
                this.$timeout(() => {
                    this.remove(this.onScreenNotificationsService.onScreenNotificationssToBeDeleted.splice(0, 1)[0]);
                })
            });
    }

    private scheduleAutohide(message: OnScreenNotificationModel) {
        setTimeout(() => {
            this.onScreenNotificationsService.onScreenNotificationsToBeDeletedSource.next(message);
        }, message.dismissAfter)
    }

    private remove(message: OnScreenNotificationModel) {
        _remove(this.notifications, function (n: OnScreenNotificationModel) {
            return n.id == message.id;
        });
    }
}

OnScreenNotificationsController.$inject = [...BaseController.injects, 'onScreenNotificationsService'];


