import { Action } from "redux";
import {
    FIREBASE_INIT_WEB, FIREBASE_INIT_WEB_ERROR, FIREBASE_LOADING, FIREBASE_NOT_LOADING,
    FIREBASE_LOAD_SCRIPT_SUCCESS, FIREBASE_LOAD_SCRIPT_ERROR, FIREBASE_GLOBAL_READY, FIREBASE_INIT_WEB_SUCCESS,
    FIREBASE_INIT_CORDOVA, FIREBASE_INIT_CORDOVA_ERROR, FIREBASE_INIT_CORDOVA_SUCCESS
} from "./firebase.actions";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";

export function firebaseLoading(): Action {
    return {
        type: FIREBASE_LOADING
    }
}

export function firebaseNotLoading(): Action {
    return {
        type: FIREBASE_NOT_LOADING
    }
}

export function firebaseInitWeb(): Action {
    return {
        type: FIREBASE_INIT_WEB
    }
}

export function firebaseInitWebError(error): ActionWPayload {
    return {
        type: FIREBASE_INIT_WEB_ERROR,
        payload: error
    }
}

export function firebaseInitWebSuccess(): Action {
    return {
        type: FIREBASE_INIT_WEB_SUCCESS
    }
}

export function firebaseLoadScriptSuccess(): Action {
    return {
        type: FIREBASE_LOAD_SCRIPT_SUCCESS
    }
}

export function firebaseLoadScriptError(error): ActionWPayload {
    return {
        type: FIREBASE_LOAD_SCRIPT_ERROR,
        payload: error
    }
}

export function firebaseInitCordova(): Action {
    return {
        type: FIREBASE_INIT_CORDOVA
    }
}

export function firebaseInitCordovaError(error): ActionWPayload {
    return {
        type: FIREBASE_INIT_CORDOVA_ERROR,
        payload: error
    }
}

export function firebaseInitCordovaSuccess(): Action {
    return {
        type: FIREBASE_INIT_CORDOVA_SUCCESS
    }
}

export function firebaseGlobalReady(): Action {
    return {
        type: FIREBASE_GLOBAL_READY
    }
}


