import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import { FirebaseState } from "../app.initial.state";
import { FIREBASE_LOADING, FIREBASE_NOT_LOADING, FIREBASE_GLOBAL_READY } from "./firebase.actions";

export function firebaseReducer(firebaseState: FirebaseState, action: ActionWPayload) {
    switch (action.type) {
        case FIREBASE_LOADING: {
            return {...firebaseState, firebaseLoading: true};
        }

        case FIREBASE_NOT_LOADING: {
            return {...firebaseState, firebaseLoading: false};
        }

        case FIREBASE_GLOBAL_READY: {
            return {...firebaseState, firebaseReady: true};
        }
    }

    return {...firebaseState};
}



