import INgModelController = angular.INgModelController;
import IAttributes = angular.IAttributes;
import IDirective = angular.IDirective;
import IScope = angular.IScope;

const stringEndingWithDotSomething: RegExp = /[\S]*(\.[A-Za-z0-9]([A-Za-z0-9-]{0,61}[A-Za-z0-9]))$/;

export function EmailWithDotValidatorDirective(): IDirective {
    return {
        restrict: 'A',
        require: '?ngModel',
        link: function (scope: IScope, elm: JQuery, attr: IAttributes, ctrl: INgModelController) {
            if (!ctrl) return;

            let name = 'emailWithDot';

            if (scope.$eval(attr[name]) == false) {
                return;
            }

            ctrl.$validators[name] = function (modelValue, viewValue) {
                var value = modelValue || viewValue;
                return ctrl.$isEmpty(value) || stringEndingWithDotSomething.test(value);
            };

            attr.$observe(name, function () {
                ctrl.$validate();
            });
        }
    };
}



