import IModule = angular.IModule;

declare var angular: angular.IAngularStatic;

import { AuthLoginComponent } from "./login/auth-login.component";
import { AuthResetPassword } from "./reset-password/auth-reset-password.component";
import { AuthToolbarComponent } from "./toolbar/auth-toolbar.component";
import { AuthSignUpComponent } from "./signup/auth-signup.component";
import { AuthLoginAlreadyLoggedInComponent } from "./login/login-already-logged-in/auth-login-already-logged-in.component";
import { AuthLoginEmailComponent } from "./login/login-email/auth-login-email.component";
import { AuthLoginHeaderComponent } from "./login/login-header/auth-login-header.component";

import { AuthConfig } from "./auth.config";

import './auth.scss';

export let AuthModule: IModule = angular.module('auth', [
    'ui.router',
    'ngAnimate'
]);

AuthModule.config(AuthConfig);
AuthModule.component('resetPassword', AuthResetPassword);
AuthModule.component('authToolbar', AuthToolbarComponent);
AuthModule.component('signUp', AuthSignUpComponent);
AuthModule.component('login', AuthLoginComponent);
AuthModule.component('loginEmail', AuthLoginEmailComponent);
AuthModule.component('loginAlreadyLoggedIn', AuthLoginAlreadyLoggedInComponent);
AuthModule.component('loginHeader', AuthLoginHeaderComponent);




