import { CordovaState } from "../app.initial.state";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import { CORDOVA_INIT, CORDOVA_RESET } from "./cordova.actions";

export function cordovaReducer(cordovaState: CordovaState, action: ActionWPayload) {
    switch (action.type) {
        case CORDOVA_INIT: {
            return {...cordovaState, cordova: action.payload};
        }
        case CORDOVA_RESET: {
            return {...cordovaState, cordova: null};
        }
    }
    return {...cordovaState};
}



