import IScope = angular.IScope;

import { BaseController } from "../../../base.controller";
import { StateService, TransitionService } from "ui-router-core";
import { NavigationService } from "../../../services/todo-convert-to-pure/navigation.service";
import { LoggingService } from "../../../services/pure/logging.service";
import { DeviceService } from "../../../services/pure/device.service";
import { INgRedux } from "ng-redux";
import { events_getEventsFromContentful, events_resetEventsState } from "../../../redux/events/events.action-creators";

export class MainToolbarController extends BaseController {

    constructor($scope: angular.IScope,
                $timeout: angular.ITimeoutService,
                $ngRedux: INgRedux,
                $transitions: TransitionService,
                public navigationService: NavigationService,
                public loggingService: LoggingService,
                public deviceService: DeviceService) {
        super($scope, $timeout, $ngRedux, $transitions);
    }

    refreshEvents() {
        this.$ngRedux.dispatch(events_resetEventsState());
        this.$ngRedux.dispatch(events_getEventsFromContentful(0, 10));
    }

    goBack() {
        if (!this.$scope.state.router.currentParams.backState) {
            return;
        }
        this.$timeout(() => {
            this.$scope.stateGo(this.$scope.state.router.currentParams.backState);
        }, 160)
    }

}

MainToolbarController.$inject =
    [
        ...BaseController.injects,
        'navigationService',
        'loggingService',
        'deviceService'
    ];





