//================================================================
// POLYFILL
//================================================================
import "babel-polyfill";
import 'dom4/build/dom4.js';

//================================================================
// VENDOR LIBRARIES
//================================================================

// *** loaded with script tag, see app-external.html
// import 'jquery';
// import 'angular';
// ***

// *** Exposed as globals with expose plugin, see expose-libraries.conf.js ***
import 'moment';
import 'fastclick';
import 'element-resize-detector/src/element-resize-detector.js';
// ***

// import 'contentful/browser.js'; // DO NOT USE, conflicts with recent lodash... see contentful service

import 'angular-messages';
import 'angular-animate';
import 'angular-local-storage';
import 'angular-ui-router'; // see https://github.com/angular-ui/ui-router/releases/
// import 'owlcarousel/owl-carousel/owl.carousel.js';

import 'ng-redux';
import 'redux-ui-router/lib/index.js';

//================================================================
// VENDOR STYLES
//================================================================

import '../../node_modules_modified/normalize.css/normalize-modified.css';
import '../../node_modules/angular/angular-csp.css';
// import 'owlcarousel/owl-carousel/owl.carousel.css';
// import 'owlcarousel/owl-carousel/owl.theme.css';

import { AppComponent } from './app.component';

//================================================================
// VARIABLES
//================================================================

//Initial viewport
let viewportHeight = 0;
let viewportWidth = 0;

declare var angular: angular.IAngularStatic;
declare var document: Document;

//================================================================
// MODULE
//================================================================

import { ComponentsModule } from './components/components';

let AppModule = angular.module('app', [
    'ui.router',
    'ngAnimate',
    'ngMessages',
    'LocalStorageModule',
    'ngRedux',
    'ng-ui-router-middleware',
    ComponentsModule.name
]);

//================================================================
// COMPONENTS
//================================================================

AppModule.component('app', AppComponent);

//================================================================
// CONFIG AND RUN BLOCKS
//================================================================

import { AppConfig } from "./app.config";
import { AppRun } from "./app.run";

AppModule.config(AppConfig);
AppModule.run(AppRun);

//================================================================
// ANIMATIONS
//================================================================

import { AppAnimations } from "./app.animations";

AppModule.animation('.app__ui-view-animate', AppAnimations);

//================================================================
// SERVICES
//================================================================

// import { ResizeSensorService } from '../shared/services/resize-sensor.service';
import { LoggingService } from "./services/pure/logging.service";
import { FacebookService } from "./services/pure/facebook.service";
import { ContentfulService } from "./services/pure/contentful.service";
import { FirebaseService } from "./services/pure/firebase.service";
import { GoogleAnalyticsService } from "./services/todo-convert-to-pure/google-analytics.service";
import { FirebaseAnalyticsService } from "./services/unused/firebase-analytics.service"
import { DeviceService } from "./services/pure/device.service";
import { OnScreenNotificationsService } from "./services/unpure/on-screen-notifications.service";
import { NavigationService } from "./services/todo-convert-to-pure/navigation.service";
import { OneSignalService } from "./services/pure/one-signal.service";
import { PushNotificationsService } from "./services/unused/push-notifications.service";
import { AnalyticsService } from "./services/pure/analytics.service";
import { GoogleLoginService } from "./services/pure/google-login.service";

AppModule.service('navigationService', NavigationService);
AppModule.service('contentfulService', ContentfulService);
AppModule.service('firebaseAnalyticsService', FirebaseAnalyticsService);
AppModule.service('analyticsService', AnalyticsService);
AppModule.service('firebaseService', FirebaseService);
AppModule.service('googleAnalyticsService', GoogleAnalyticsService);
AppModule.service('facebookService', FacebookService);
AppModule.service('loggingService', LoggingService);
AppModule.service('oneSignalService', OneSignalService);
AppModule.service('deviceService', DeviceService);
AppModule.service('onScreenNotificationsService', OnScreenNotificationsService);
AppModule.service('pushNotificationsService', PushNotificationsService);
AppModule.service('googleLoginService', GoogleLoginService);

export let injector: angular.auto.IInjectorService = angular.bootstrap(jQuery('body'), [AppModule.name]);





