import IQService = angular.IQService;
import IFilterService = angular.IFilterService;
import IDeferred = angular.IDeferred;

import { BaseService } from "../base.service";
import { LoggingService } from "./logging.service";

export class FacebookService extends BaseService {

    constructor($q: IQService, $filter: IFilterService, loggingService: LoggingService) {
        super($q, $filter, loggingService);
    }

    //================================================================
    // WEB
    //================================================================

    jQueryLoadFacebookSDKScript() {
        return jQuery.getScript("https://connect.facebook.net/en_US/sdk.js")
    }

    getLoginStatus(cb) {
        this.getFBGlobal().getLoginStatus(cb)
    }

    getFBGlobal() {
        if (window['FB']) {
            return window['FB'];
        }
    }

    //================================================================
    // CORDOVA
    //================================================================

    signOutCordova() {
        if (this.getFBPluginGlobal()) {
            this.getFBPluginGlobal().logout();
        }
    }

    getLoginStatusCordova(cb, forceServerRoundTrip: boolean = true) {
        if (this.getFBPluginGlobal()) {
            this.getFBPluginGlobal().getLoginStatus(cb, forceServerRoundTrip)
        }
    }

    getFBPluginGlobal() {
        if (window['facebookConnectPlugin']) {
            return window['facebookConnectPlugin'];
        }
    }
}

FacebookService.$inject = [...BaseService.injects];



