import IQService = angular.IQService;
import IFilterService = angular.IFilterService;
import IDeferred = angular.IDeferred;

import { BaseService } from "../base.service";
import { LoggingService } from "./logging.service";
import { GOOGLE_LOGIN_SDK_URL } from "../../app.constants";

import { GoogleLoginPluginAuthResposeModel } from "../../models/google-login-plugin-auth-response.model";

export class GoogleLoginService extends BaseService {

    constructor($q: IQService, $filter: IFilterService, loggingService: LoggingService) {
        super($q, $filter, loggingService);
    }

    //================================================================
    // WEB
    //================================================================

    signOut() {
        return new Promise<any>((resolve, reject) => {
            if (this.getGoogleLoginGlobal()
                && this.getGoogleLoginGlobal().auth2
                && this.getGoogleLoginGlobal().auth2.getAuthInstance()) {

                resolve(this.getGoogleLoginGlobal().auth2.getAuthInstance().signOut());
        } else {
            resolve();
        }
    });

    }

    getGoogleAuthInstance() {
        if (this.getGoogleLoginGlobal()){
            return this.getGoogleLoginGlobal().auth2.getAuthInstance();
        }
    }

    revokeGoogleLoginAuthorization() {
        if (this.getGoogleLoginGlobal()){
            this.getGoogleLoginGlobal().auth2.getAuthInstance().disconnect();
        }
    }

    jQueryLoadGoogleLoginSDKScript() {
        return jQuery.getScript(GOOGLE_LOGIN_SDK_URL)
    }

    getGoogleLoginGlobal() {
        if (window['gapi']) {
            return window['gapi'];
        }
    }

    //================================================================
    // CORDOVA
    //================================================================

    signInCordova(config,
        success: (response: GoogleLoginPluginAuthResposeModel) => void,
        error: (error: GoogleLoginPluginAuthResposeModel) => void) {
        if (this.getGooglePluginGlobal()){
            this.getGooglePluginGlobal().login(config, success, error);
        }
    }

    trySilentLogin(config,
        success: (error: GoogleLoginPluginAuthResposeModel) => void,
        error: (error: GoogleLoginPluginAuthResposeModel) => void) {
        if (this.getGooglePluginGlobal()){
            this.getGooglePluginGlobal().trySilentLogin(config, success, error);
        }
    }

    signOutCordova(cb: (response) => any) {
        if (this.getGooglePluginGlobal()){
            this.getGooglePluginGlobal().logout(cb)            
        }
    }

    getGooglePluginGlobal(): any {
        if (window['plugins'] && window['plugins']['googleplus']) {
            return window['plugins']['googleplus'];
        }
    }

}

GoogleLoginService.$inject = [...BaseService.injects];



