import { Dispatch } from "redux";

import { injector } from "../../app";
import { IGlobalAppState } from "../../interfaces/global-app-state.interface";

import { IListenerMiddleWareListener } from "../app-listener.middleware";
import { ContentfulService } from "../../services/pure/contentful.service";

import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";

import {
    EVENTS_GET_EVENT_CONTENTFUL, EVENTS_GET_EVENTS_CONTENTFUL, EVENTS_GET_PAST_EVENTS_CONTENTFUL,
    EVENTS_GET_UPCOMING_EVENTS_CONTENTFUL,
    EVENTS_GO_TO_SELECTED_EVENT
} from "./events.actions";
import {
    events_getEventsFromContentfulError, events_getEventsFromContentfulSuccess, events_loading, events_notLoading,
    events_setSelectedEvent,
    events_setEvents,
    events_setSkip, events_getEventFromContentfulError, events_getEventFromContentfulSuccess,
    events_getEventsFromContentful, events_setUpcomingEvents, events_setPastEvents, events_showPastEvents,
    events_showUpcomingEvents, events_getPastEventsFromContentful, events_getUpcomingEventsFromContentful
} from "./events.action-creators";
import { stateGo } from "redux-ui-router";

const _concat = require('lodash/concat');
const _uniqBy = require('lodash/uniqBy');

export let eventsListener: IListenerMiddleWareListener = {
    [EVENTS_GET_EVENTS_CONTENTFUL]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let contentfulService = <ContentfulService>injector.get('contentfulService');

        dispatch(events_loading());

        contentfulService.getEvents(state.app.contentful.contentfulClient, action.payload.skip, action.payload.limit)
            .then((contentfulEvents: any) => {
                if (contentfulEvents && contentfulEvents.length) {
                    let events = filterUniques(state.app.events.events, contentfulEvents);
                    setTimeout(() => {
                        dispatch(events_getEventsFromContentfulSuccess());
                    }, 400);
                    dispatch(events_setEvents(events));
                    dispatch(events_setSkip(events.length));
                    return events;
                } else {
                    throw new Error('Could not retrieve events.');
                }
            })
            .then((events) => {
                let upcomingEvents = contentfulService.getUpcomingEvents(events);

                dispatch(events_setUpcomingEvents(upcomingEvents));

                if (upcomingEvents && upcomingEvents.length) {
                    if (state.app.events.eventsShowUpcoming) {
                        dispatch(events_showUpcomingEvents());
                    }
                }

                return events;
            })
            .then((events) => {
                let pastEvents = contentfulService.getPastEvents(events);

                dispatch(events_setPastEvents(pastEvents));

                if (pastEvents && pastEvents.length) {
                    if (state.app.events.eventsShowPast) {
                        dispatch(events_showPastEvents());
                    }
                }
            })
            .catch(error => {
                dispatch(events_getEventsFromContentfulError(error));
            })
            .finally(() => {
                dispatch(events_notLoading());
            });

        function filterUniques(events, contentfulEvents) {
            return _uniqBy(_concat(events, contentfulEvents), (event) => {
                return event.sys.id;
            });
        }
    },
    [EVENTS_GET_EVENT_CONTENTFUL]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let contentfulService = <ContentfulService>injector.get('contentfulService');

        dispatch(events_loading());

        contentfulService.getEventBySysId(state.app.contentful.contentfulClient, action.payload)
            .then((contentfulEvent: any) => {
                if (contentfulEvent) {
                    setTimeout(() => {
                        dispatch(events_getEventFromContentfulSuccess());
                    }, 400);
                    dispatch(events_setSelectedEvent(contentfulEvent));
                } else {
                    throw new Error('Could not retrieve event.');
                }
            })
            .catch(error => {
                dispatch(events_getEventFromContentfulError(error));
            })
            .finally(() => {
                dispatch(events_notLoading());
            });
    },
    [EVENTS_GO_TO_SELECTED_EVENT]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        if (state.app.events.selectedEvent && state.app.events.selectedEvent.sys.id) {
            dispatch(stateGo('main.player', {
                eventId: state.app.events.selectedEvent.sys.id
            }));
        }
    }
};



