import IQService = angular.IQService;

import { INgRedux } from "ng-redux";

export class DeviceService {

    constructor(public $q: IQService, public $ngRedux: INgRedux) {
    }

    isCordovaApp() {
        return this.getCordovaGlobal()!!;
    }

    isiOS() {
        return this.matchUserAgent(/iPhone|iPad|iPod/i);
    }

    isChromeiOS() {
        return this.matchUserAgent(/CriOS/);
    }

    isAndroid() {
        return this.matchUserAgent(/Android/i);
    }

    matchUserAgent(regExp: RegExp) {
        let matches = navigator.userAgent.match(regExp);
        return matches !== null && matches.length !== 0;
    }

    isWeb() {
        return !this.isCordovaApp();
    }

    getPlatform() {
        if (this.isCordovaApp()) {
            if (this.isiOS()) {
                return 'ios';
            } else {
                return 'android';
            }
        } else {
            return 'web'
        }
    }

    getCordovaGlobal() {
        if (window['cordova']) {
            return window['cordova'];
        }
    }
}

DeviceService.$inject = ['$q', '$ngRedux'];



