import IQService = angular.IQService;
import IQResolveReject = angular.IQResolveReject;
import IDeferred = angular.IDeferred;

import { LogEventModel } from "../../models/log-event.model";
import { DeviceService } from "./device.service";
import { GoogleAnalyticsService } from "../todo-convert-to-pure/google-analytics.service";
import { FirebaseAnalyticsService } from "../unused/firebase-analytics.service";

export class AnalyticsService {

    constructor(public $q: IQService,
                public deviceService: DeviceService,
                public googleAnalyticsService: GoogleAnalyticsService,
                public firebaseAnalyticsService: FirebaseAnalyticsService) {
    }

    log(logEvent: LogEventModel) {
        // warnings and errors are logged to analytics, depending on platform
            if (this.deviceService.isCordovaApp()) {
                this.firebaseAnalyticsService.sendEvent();
            } else {
                this.googleAnalyticsService.sendEvent(logEvent);
            }
    }
}

AnalyticsService.$inject = ['$q', 'deviceService', 'googleAnalyticsService', 'firebaseAnalyticsService'];





