import IQService = angular.IQService;
import IDeferred = angular.IDeferred;
import IQProvider = angular.IQProvider;
import IFilterService = angular.IFilterService;
import IPromise = angular.IPromise;

import { INgRedux } from "ng-redux";

const _map = require('lodash/map');

import { BaseService } from "../base.service";
import { LoggingService } from "./logging.service";
import { DeviceService } from "./device.service";
import { GoogleLoginService } from "./google-login.service";

import { FacebookService } from "./facebook.service";
import {
    FACEBOOK_LOGIN_SCOPES,
    FACEBOOK_OAUTH_REDIRECT_URL_PROD,
    FIREBASE_INIT_CORDOVA_OPTIONS_PROD,
    FIREBASE_INIT_WEB_OPTIONS_PROD,
    FIREBASE_SDK_URL, GOOGLE_LOGIN_CORDOVA_OPTIONS
} from "../../app.constants";
import { FacebookAuthResponse } from "../../interfaces/facebook-auth-response.interface";
import { GoogleLoginAuthResponseModel } from "../../models/google-login-auth-user-response.model";
import { GoogleLoginPluginAuthResposeModel } from "../../models/google-login-plugin-auth-response.model";

export class FirebaseService extends BaseService {

    constructor($q: IQService,
                $filter: IFilterService,
                loggingService: LoggingService,
                public deviceService: DeviceService,
                public facebookService: FacebookService,
                public googleLoginService: GoogleLoginService,
                public $ngRedux: INgRedux) {
        super($q, $filter, loggingService);
    }

    signInWithEmailAndPassword(email, password) {
        let deferred = this.$q.defer();

        deferred.resolve(this.getFirebaseGlobal().auth().signInWithEmailAndPassword(email, password));

        return deferred.promise;
    }

    createUserWithEmailAndPassword(email, password) {
        let deferred = this.$q.defer();

        deferred.resolve(this.getFirebaseGlobal().auth().createUserWithEmailAndPassword(email, password));

        return deferred.promise;
    }

    resetPassword(email) {
        let deferred = this.$q.defer();

        deferred.resolve(this.getFirebaseGlobal().auth().sendPasswordResetEmail(email));

        return deferred.promise;
    }


    signOut() {
        this.getFirebaseGlobal().auth().signOut();
    }

    //================================================================
    // WEB
    //================================================================

    initWeb() {

        //================================================================
        // STAGING
        //================================================================

        // this.getFirebaseGlobal().initializeApp(FIREBASE_INIT_OPTIONS_STAGING);

        //================================================================
        // PROD
        //================================================================

        this.getFirebaseGlobal().initializeApp(FIREBASE_INIT_WEB_OPTIONS_PROD);
    }

    signInGoogle(GoogleUser?: GoogleLoginAuthResponseModel) {
        let deferred = this.$q.defer();

        if (GoogleUser) {
            deferred.resolve(this.passGoogleLoginCredentialToFirebase(GoogleUser));
        } else {
            let GoogleAuth = this.googleLoginService.getGoogleAuthInstance();

            if (GoogleAuth.isSignedIn.get()) {
                // pass credentials to firebase auth
                deferred.resolve(this.passGoogleLoginCredentialToFirebase(GoogleAuth.currentUser.get()));
            } else {
                // redirection to OAuth consent screen
                GoogleAuth.signIn();
            }
        }

        return deferred.promise;
    }

    jQueryLoadFirebaseScript() {
        return jQuery.getScript(FIREBASE_SDK_URL)
    }

    signInFB(response?: FacebookAuthResponse) {
        let deferred = this.$q.defer();

        if (response) {
            this.handleFBLoginStatusResponse.bind(this)(deferred, response);
        } else {
            this.facebookService.getLoginStatus((response) => {
                this.handleFBLoginStatusResponse.bind(this)(deferred, response)
            });
        }

        return deferred.promise;
    }

    handleFBLoginStatusResponse(deferred, response: FacebookAuthResponse) {
        // connected.       The person is logged into Facebook, and has logged into your app.
        // not_authorized.  The person is logged into Facebook, but has not logged into your app.
        // unknown.         The person is not logged into Facebook, so you don't know if they've logged into your app.
        switch (response.status) {
            case 'connected': {
                deferred.resolve(this.passFacebookCredentialToFirebase(response));
                break;
            }

            case 'not_authorized':
            case 'unknown': {
                // location.href = encodeURI(FACEBOOK_OAUTH_REDIRECT_URL_DEV);
                location.href = encodeURI(FACEBOOK_OAUTH_REDIRECT_URL_PROD);
                deferred.resolve();
                break;
            }
        }
    }

    //================================================================
    // CORDOVA
    //================================================================

    initCordova() {

        //================================================================
        // STAGING
        //================================================================

        // this.getFirebaseGlobal().initializeApp(FIREBASE_INIT_CORDOVA_OPTIONS_STAGING);

        //================================================================
        // PROD
        //================================================================

        this.getFirebaseGlobal().initializeApp(FIREBASE_INIT_CORDOVA_OPTIONS_PROD);
    }

    signInGoogleCordova(pluginAuthResponse?: GoogleLoginPluginAuthResposeModel) {
        let deferred = this.$q.defer();

        try {
            this.googleLoginService
                .signInCordova(GOOGLE_LOGIN_CORDOVA_OPTIONS, response => {
                    deferred.resolve(this.passGoogleLoginPluginCredentialToFirebase(response));
                }, error => {
                    deferred.reject(error);
                });

        } catch (error) {
            deferred.reject(error)
        }

        return deferred.promise;
    }

    signInFBCordova(response?: FacebookAuthResponse) {
        let deferred = this.$q.defer();

        if (response) {
            this.handleFBLoginStatusResponseCordova.bind(this)(deferred, response);
        } else {
            this.facebookService
                .getLoginStatusCordova(response => {
                    this.handleFBLoginStatusResponseCordova.bind(this)(deferred, response)
                });
        }

        return deferred.promise;
    }

    handleFBLoginStatusResponseCordova(deferred, response: FacebookAuthResponse) {
        switch (response.status) {
            case 'connected': {
                deferred.resolve(this.passFacebookCredentialToFirebase(response));
                break;
            }

            case 'not_authorized':
            case 'unknown': {
                this.facebookService.getFBPluginGlobal()
                    .login(FACEBOOK_LOGIN_SCOPES, (response: FacebookAuthResponse) => {
                        deferred.resolve(this.passFacebookCredentialToFirebase(response));
                    }, (error) => {
                        deferred.reject(error);
                    });
                break;
            }
        }
    }

    getDeviceToken() {
        this.getFirebasePluginGlobal().getToken(function (token) {
            // save this server-side and use it to push notifications to this device
            console.log(token);
        }, function (error) {
            console.error(error);
        });
    }

    onTokenRefresh() {
        this.getFirebasePluginGlobal().onTokenRefresh(function (token) {
            // save this server-side and use it to push notifications to this device
            console.log(token);
        }, function (error) {
            console.error(error);
        });
    }

    onNotificationOpen() {
        this.getFirebasePluginGlobal().onNotificationOpen(function (notification) {
            // save this server-side and use it to push notifications to this device
            console.log(notification);
        }, function (error) {
            console.error(error);
        });
    }

    grantPermissioniOS() {
        this.getFirebasePluginGlobal().hasPermission(function (data) {
            console.log(data.isEnabled);
        });
    }

    setBadgeNumber(badgeNumber: number = 0) {
        this.getFirebasePluginGlobal().setBadgeNumber(badgeNumber);
    }

    getBadgeNumber() {
        this.getFirebasePluginGlobal().getBadgeNumber(function (n) {
            console.log(n);
        });
    }

    getFirebasePluginGlobal() {
        if (window['FirebasePlugin']) {
            return window['FirebasePlugin'];
        }
    }

    //================================================================
    // HELPERS METHODS
    //================================================================

    private passFacebookCredentialToFirebase(response: FacebookAuthResponse) {
        return this.signInWithCredential(
            this.getFirebaseGlobal()
                .auth
                .FacebookAuthProvider
                .credential(response.authResponse.accessToken))
    }

    private passGoogleLoginCredentialToFirebase(GoogleUser: any) {
        return this.signInWithCredential(
            this.getFirebaseGlobal()
                .auth
                .GoogleAuthProvider
                .credential(GoogleUser.getAuthResponse().id_token))
    }

    private passGoogleLoginPluginCredentialToFirebase(pluginAuthResponse: GoogleLoginPluginAuthResposeModel) {
        return this.signInWithCredential(
            this.getFirebaseGlobal()
                .auth
                .GoogleAuthProvider
                .credential(pluginAuthResponse.idToken))
    }

    getFirebaseGlobal() {
        if (window['firebase']) {
            return window['firebase'];
        }
    }

    signInWithCredential(credential) {
        return this.getFirebaseGlobal().auth().signInWithCredential(credential);
    }

    //================================================================
    // TODO REMOVE
    //================================================================

    authProviderIsFB() {
        return _map(this.getFirebaseGlobal().auth().currentUser.providerData, (d: any) => {
            return d.providerId === 'facebook.com'
        })[0]
    }

    checkIfUserIsEqual(authProviderResponse, firebaseUser) {
        if (firebaseUser && authProviderResponse) {
            var providerData = firebaseUser.providerData;
            for (var i = 0; i < providerData.length; i++) {
                if (providerData[i].providerId === this.getFirebaseGlobal().auth.FacebookAuthProvider.PROVIDER_ID &&
                    providerData[i].uid === authProviderResponse.userID) {
                    // We don't need to re-auth the Firebase connection.
                    return true;
                }
            }
        }
        return false;
    }

}

FirebaseService.$inject = [
    ...BaseService.injects,
    'deviceService',
    'facebookService',
    'googleLoginService',
    '$ngRedux'
];



