import IAngularStatic = angular.IAngularStatic;
import IQService = angular.IQService;

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/observable/empty';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/throttleTime';
import 'rxjs/add/operator/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/filter';

declare var angular: IAngularStatic;

import { INgRedux } from "ng-redux";

import { TransitionService, Transition, State, TargetState } from "ui-router-core";
import { FacebookService } from "./services/pure/facebook.service";
import { BaseController } from "./base.controller";
import { GoogleAnalyticsService } from "./services/todo-convert-to-pure/google-analytics.service";
import { DeviceService } from "./services/pure/device.service";
import { LoggingService } from "./services/pure/logging.service";
import { FirebaseService } from "./services/pure/firebase.service";
import { FirebaseAnalyticsService } from "./services/unused/firebase-analytics.service";
import { OneSignalService } from "./services/pure/one-signal.service";
import { ContentfulService } from "./services/pure/contentful.service";
import { NavigationService } from "./services/todo-convert-to-pure/navigation.service";
import { LogEventModel } from "./models/log-event.model";

import { appNotLoading, appLoading } from "./redux/app.action-creators";
import { cordovaInit } from "./redux/cordova/cordova.action-creators";
import {
    googleLoginDisable, googleLoginInitCordova, googleLoginInitWeb,
    googleLoginLoading
} from "./redux/google-login/google-login.action-creators";
import {
    googleAnalyticsLoading,
    googleAnalyticsNotLoading
} from "./redux/google-analytics/google-analytics.action-creators";
import {
    contentfulInitWeb,
    contentfulLoading, contentfulNotLoading,
    contentfulSetClient
} from "./redux/contentful/contentful.action-creators";
import {
    firebaseInitCordova, firebaseInitWeb, firebaseLoading,
    firebaseNotLoading
} from "./redux/firebase/firebase.action-creators";
import {
    facebookDisableLogin, facebookEnableLogin, facebookInitCordova, facebookInitWeb, facebookLoading,
    facebookNotLoading
} from "./redux/facebook/facebook.action-creators";
import { auth_setUser } from "./redux/auth/auth.action-creators";
import { device_setDeviceType, device_setPlatform } from "./redux/device/device.action-creators";

export class AppController extends BaseController {

    logEvent = this.loggingService.log.bind(this.loggingService);

    constructor($scope: angular.IScope,
                $timeout: angular.ITimeoutService,
                $ngRedux: INgRedux,
                $transitions: TransitionService,
                public $q: IQService,
                public firebaseService: FirebaseService,
                public facebookService: FacebookService,
                public googleAnalyticsService: GoogleAnalyticsService,
                public firebaseAnalyticsService: FirebaseAnalyticsService,
                public loggingService: LoggingService,
                public deviceService: DeviceService,
                public oneSignalService: OneSignalService,
                public navigationService: NavigationService,
                public contentfulService: ContentfulService,
                public $element: JQuery) {
        super($scope, $timeout, $ngRedux, $transitions);
    }

    $onInit() {
        // can be removed once we get rid of the slido embed.
        this.changeDocumentBackgroundColor();
    }

    $postLink() {
        this.configJQueryCaching();
        this.initFastClick();
        this.setInitialAnimation();

        this.deviceService.isiOS() ?
            this.$ngRedux.dispatch(device_setDeviceType('ios')) :
            this.$ngRedux.dispatch(device_setDeviceType('android'));

        if (this.deviceService.isCordovaApp()) {

            ////////////////////////////////
            // Cordova app initialization //
            ////////////////////////////////

            this.$ngRedux.dispatch(device_setPlatform('cordova'));
            this.initCordova();

        } else {

            ////////////////////////////
            // Web app initialization //
            ////////////////////////////

            this.$ngRedux.dispatch(device_setPlatform('web'));
            this.initWebApp();
        }
    }

    changeDocumentBackgroundColor() {
        this.$transitions.onSuccess({
            to: (state) => state.name === 'login'
            || state.name === 'resetPassword'
            || state.name === 'signup'
        }, (transition: Transition) => {
            jQuery('html').removeClass('app__html--white').removeClass('app__html--grey');
        });
        this.$transitions.onSuccess({
            to: (state) => (state.name === 'main' || state.parent.name === 'main') && state.name !== 'main.eventDetails'
        }, (transition: Transition) => {
            jQuery('html').addClass('app__html--white').removeClass('app__html--grey');
        });
        this.$transitions.onSuccess({
            to: (state) => state.name === 'main.eventDetails'
        }, (transition: Transition) => {
            jQuery('html').addClass('app__html--grey');
        })
    }

    displayWatchersCount() {
        var root = angular.element(document.documentElement);
        var watcherCount = 0;

        function getElemWatchers(element) {
            var isolateWatchers = getWatchersFromScope(element.data().$isolateScope);
            var scopeWatchers = getWatchersFromScope(element.data().$scope);
            var watchers = scopeWatchers.concat(isolateWatchers);
            angular.forEach(element.children(), function (childElement) {
                watchers = watchers.concat(getElemWatchers(angular.element(childElement)));
            });
            return watchers;
        }

        function getWatchersFromScope(scope) {
            if (scope) {
                return scope.$$watchers || [];
            } else {
                return [];
            }
        }

        console.log(getElemWatchers(root).length);
    }

    removeInitialLoader() {
        this.$timeout(() => {
            jQuery('#loader').addClass('not-loading');
        }, 50);
    }

    initFastClick() {
        FastClick.attach(document.body); // Remove 300ms click delay on old Safari/Android versions
    }

    setInitialAnimation() {
        this.$timeout(() => {
            let uiView = this.$element.find('.app__wrapper > [ui-view]');
            if (!uiView.hasClass('app-animations__enter-right') && !uiView.hasClass('app-animations__enter-left')) {
                uiView.addClass('app-animations__fade-in');
            }
        }, 200)
    }

    //================================================================
    // WEB
    //================================================================

    configJQueryCaching() {
        jQuery.ajaxSetup({
            cache: true
        });
    }

    initWebApp() {
        this.$ngRedux.dispatch(appLoading());

        this.initWebFirebase();
        this.initWebContentful();
        this.initWebGoogleLogin();
        this.initWebFacebook();

        this.initWebGA();
        this.initWebOneSignal();
    }

    initWebGA() {
        this.$ngRedux.dispatch(googleAnalyticsLoading());

        this.googleAnalyticsService.initWeb()
            .then(this.logEvent)
            .then(() => {
                this.$ngRedux.dispatch(googleAnalyticsNotLoading());
            })
            .then(() => this.googleAnalyticsService.sendPageViewEvent())
            .then(this.logEvent)
            .catch(this.logEvent);
    }

    initWebContentful() {
        this.$ngRedux.dispatch(contentfulInitWeb());
    }

    initWebFirebase() {
        this.$ngRedux.dispatch(firebaseInitWeb());
        this.watchForWebFirebase();
    }

    // TODO move to redux listeners
    watchForWebFirebase() {
        let unregisterWatchForWebFirebase = this.$scope.$watch('state.app.auth.user', (user: any, oldValue: any) => {
            if (user || user === null) {
                this.$ngRedux.dispatch(appNotLoading());
                this.removeInitialLoader();
                this.setInitialAnimation();
                unregisterWatchForWebFirebase();
            }
        }, false);
    }

    initWebGoogleLogin() {
        this.$ngRedux.dispatch(googleLoginInitWeb());
    }

    initWebFacebook() {
        this.$ngRedux.dispatch(facebookInitWeb());
    }

    initWebOneSignal() {
        this.$timeout(() => {
            this.oneSignalService.initWeb()
                .then(this.logEvent)
                .catch(this.logEvent);
        }, 3000);
    }

    //================================================================
    // CORDOVA
    //================================================================

    initCordova() {
        document.addEventListener('deviceready', (event) => {
            this.$ngRedux.dispatch(appLoading());

            this.initCordovaFirebase();
            this.initWebContentful(); // works for cordova
            this.initCordovaFacebook();
            this.initCordovaGoogleLogin();
        });
    }

    initCordovaFirebase() {
        this.$ngRedux.dispatch(firebaseInitCordova());
        this.watchForWebFirebase();
    }

    initCordovaGoogleLogin() {
        this.$ngRedux.dispatch(googleLoginInitCordova());
    }

    initCordovaFacebook() {
        this.$ngRedux.dispatch(facebookInitCordova());
    }

}

AppController.$inject = [
    ...BaseController.injects,
    '$q',
    'firebaseService',
    'facebookService',
    'googleAnalyticsService',
    'firebaseAnalyticsService',
    'loggingService',
    'deviceService',
    'oneSignalService',
    'navigationService',
    'contentfulService',
    '$element'
];


