import IController = angular.IController;
import IScope = angular.IScope;

const _assign = require('lodash/assign');

import { stateGo, stateReload, stateTransitionTo } from 'redux-ui-router/lib/index.js';
import { TransitionService } from "ui-router-core";
import { INgRedux } from "ng-redux";

import { IScopeExtended } from "./interfaces/scope-extended.interface";

const routerActions = {stateGo, stateReload, stateTransitionTo};

export class BaseController implements IController {

    static injects = ['$scope', '$timeout', '$ngRedux', '$transitions'];

    $scope: IScopeExtended;

    constructor($scope: angular.IScope,
                public $timeout: angular.ITimeoutService,
                public $ngRedux: INgRedux,
                public $transitions: TransitionService) {

        // overloads scope with redux store
        this.$scope = _assign($scope, {
            state: {}
        });

        let disconnect = $ngRedux.connect((state) => {
            return {
                state: state
            }
        }, routerActions)($scope);

        this.$scope.$on('$destroy', disconnect)
    }
}


