import { TargetState, State, StateService, UIRouter, Transition, TransitionService } from "ui-router-core";
import { INgRedux } from "ng-redux";

import IQService = angular.IQService;
import IDeferred = angular.IDeferred;
import IPromise = angular.IPromise;
import ITimeoutService = angular.ITimeoutService;
import IRootScopeService = angular.IRootScopeService;

import { FirebaseService } from "./services/pure/firebase.service";

import { auth_setTargetState } from "./redux/auth/auth.action-creators";

export function AppRun($rootScope: IRootScopeService,
                       $ngRedux: INgRedux,
                       $transitions: TransitionService,
                       $timeout: ITimeoutService) {


    //================================================================
    // AUTHENTICATION INTERCEPTOR
    //================================================================

    let requiresAuthCriteria = {
        to: (state: State) => {
            return state.params && state.params.authRequired && state.params.authRequired.config.value;
        }
    };

    let validateAuth = ($transition$: Transition) => {
        let firebaseService: FirebaseService = $transition$.injector().get('firebaseService');
        let $ngRedux: INgRedux = $transition$.injector().get('$ngRedux');
        let $state: any = $transition$.router.stateService;
        let state = $ngRedux.getState();

        // allow route when we have a valid user
        if (state.app.auth.user) {
            return;
        }

        let targetState: TargetState = $state.target('login', undefined, {
            location: false,
            reload: false
        });

        // we know the state when firebase has initialized,
        // this can happen when user hit the back button after logging out
        if (state.app.firebase.firebaseReady && !state.app.firebase.firebaseLoading) {
            return targetState;
        }

        // if firebase is loading, we register a promise that
        // resolves when we have information on the auth status of the user
        return new Promise<boolean | void | TargetState>((resolve, reject) => {
            let unsubscribeReduxEvents = $ngRedux.subscribe(() => {
                // important to get fresh state
                let state = $ngRedux.getState();

                if (!state.app.firebase.firebaseLoading && state.app.firebase.firebaseReady) {
                    if (firebaseService.getFirebaseGlobal()) {
                        let onAuthStateChangedUnsubscribe = firebaseService.getFirebaseGlobal()
                            .auth()
                            .onAuthStateChanged((user) => {
                                if (user === null) {
                                    resolve(<TargetState>targetState);
                                } else if (user) {
                                    resolve();
                                }

                                onAuthStateChangedUnsubscribe();
                            });

                        unsubscribeReduxEvents();
                    }
                }
            })
        });
    };

    $transitions.onBefore(requiresAuthCriteria, validateAuth, {priority: 10});

    //================================================================
    // TARGET STATE INTERCEPTOR
    //================================================================

    let loginAndLoginWEmailStatesCriteria = {
        to: (state: State) => {
            return state.name === 'login';
        }
    };

    let setTargetStateInAppState = ($transition$: Transition) => {
        let $ngRedux: INgRedux = $transition$.injector().get('$ngRedux');
        // let $state: any = $transition$.router.stateService;
        // let state = $ngRedux.getState();

        if ($transition$.redirectedFrom() != null) {
            // The auth was redirected to the login state (e.g., via the requiresAuth hook when trying to activate contacts)
            // Return to the original attempted target state (e.g., contacts)
            $ngRedux.dispatch(auth_setTargetState($transition$.redirectedFrom().targetState()));
            return;
        }

        let $state: StateService = $transition$.injector().get('$state');

        // let $state = $transition$.router.stateService;

        // The auth was not redirected to the login state; they directly activated the login state somehow.
        // Return them to the state they came from.
        if ($transition$.from().name !== ''
            && $transition$.from().name !== 'signup'
            && $transition$.from().name !== 'main.settings'
            && $transition$.from().name !== 'login'
            && $transition$.from().name !== 'loginEmail'
            && $transition$.from().name !== 'resetPassword') {
            $ngRedux.dispatch(auth_setTargetState($state.target($transition$.from(), $transition$.params("from"))));
            return;
        }

        // If the fromState's name is empty, then this was the initial transition. Just return them to the home state
        $ngRedux.dispatch(auth_setTargetState($state.target('main')));
    };

    $transitions.onFinish(loginAndLoginWEmailStatesCriteria, setTargetStateInAppState);
}

AppRun.$inject = [
    '$rootScope',
    '$ngRedux',
    '$transitions',
    '$timeout',
];



