import { BaseController } from "../../base.controller";
import { TransitionService } from "ui-router-core";
import { INgRedux } from "ng-redux";

export class MainController extends BaseController {

    constructor($scope: angular.IScope,
                $timeout: angular.ITimeoutService,
                $ngRedux: INgRedux,
                $transitions: TransitionService,
                public $element: JQuery) {
        super($scope, $timeout, $ngRedux, $transitions);
    }

    $postLink() {
        this.$timeout(() => {
            let activeView = this.$element.find('.main__scrolling.main__scrolling--active');
            if (activeView && activeView.length) {
                activeView.addClass('main-animations__enter');
            }
        }, 300);
    }
}

MainController.$inject = [...BaseController.injects, '$element'];



