import IQService = angular.IQService;
import IFilterService = angular.IFilterService;
import ITimeoutService = angular.ITimeoutService;
import { IAngularStatic } from "angular";

declare var angular: IAngularStatic;

// import TweenLite from 'gsap/src/uncompressed/TweenLite.js';
// import CSSPlugin from 'gsap/src/uncompressed/plugins/CSSPlugin.js';
// import ScrollToPlugin from 'gsap/src/uncompressed/plugins/ScrollToPlugin.js';
// import TimelineLite from 'gsap/src/uncompressed/TimelineLite.js';

import { BaseService } from "../base.service";
import { TransitionService, Transition, StateService } from "ui-router-core";
import { LoggingService } from "../pure/logging.service";
import { DeviceService } from "../pure/device.service";

export class NavigationService extends BaseService {

    lastBackState: string = null;

    allowAnimation = false;
    comingFromBackButton = false;
    browserNavigationButtons: boolean = false;

    constructor($q: IQService,
                $filter: IFilterService,
                loggingService: LoggingService,
                public $transitions: TransitionService,
                public $timeout: ITimeoutService,
                public $state: StateService,
                public deviceService: DeviceService) {
        super($q, $filter, loggingService);
        this.trackNavigation();
    }

    trackNavigation() {
        this.$transitions.onSuccess({to: x => true}, (transition: Transition) => {
            this.comingFromBackButton = !!transition.targetState().params()['comingFromBackButton'];
            this.browserNavigationButtons = transition.options().source === 'url';
        }, {
            bind: this
        });
    }

    isAnimationAllowed() {
        return !this.browserNavigationButtons;
    }
}

NavigationService.$inject = [...BaseService.injects, '$transitions', '$timeout', '$state', 'deviceService'];




