import { AppState } from "./app.initial.state";
import { ActionWPayload } from "../interfaces/redux-action-w-payload.interface";

import {
    AUTH_RESET_USER, AUTH_SET_USER, AUTH_SIGN_IN_ADD_INVALID_EMAIL,
    AUTH_SIGN_IN_EMAIL, AUTH_SIGN_IN_EMAIL_ERROR_WRONG_PASSWORD, AUTH_SIGN_IN_FACEBOOK,
    AUTH_SIGN_IN_REMOVE_INVALID_EMAIL,
    AUTH_SIGN_IN_RESET_INVALID_EMAILS, AUTH_SIGN_IN_FACEBOOK_ERROR, AUTH_SIGN_IN_FACEBOOK_SUCCESS,
    AUTH_SIGN_IN_EMAIL_OTHER_ERROR, AUTH_SIGN_OUT, AUTH_SIGN_IN_FACEBOOK_WITH_TOKEN, AUTH_SIGN_IN_GOOGLE_WITH_TOKEN,
    AUTH_SIGN_IN_GOOGLE_LOGIN_SUCCESS, AUTH_SIGN_IN_GOOGLE_LOGIN_ERROR, AUTH_SIGN_IN_GOOGLE_LOGIN, AUTH_SIGN_UP_EMAIL,
    AUTH_RESET_PASSWORD, AUTH_SIGN_UP_EMAIL_SUCCESS, AUTH_SIGN_UP_EMAIL_ERROR, AUTH_RESET_PASSWORD_SUCCESS,
    AUTH_RESET_PASSWORD_ERROR, AUTH_SIGN_OUT_SUCCESS, AUTH_SIGN_OUT_ERROR, AUTH_SIGN_IN_FACEBOOK_WITH_TOKEN_CORDOVA,
    AUTH_SIGN_IN_FACEBOOK_CORDOVA, AUTH_SIGN_IN_FACEBOOK_CANCELLED_CORDOVA, AUTH_SIGN_IN_FACEBOOK_ERROR_CORDOVA,
    AUTH_SIGN_IN_FACEBOOK_SUCCESS_CORDOVA, AUTH_SET_TARGET_STATE, AUTH_RESET_TARGET_STATE, AUTH_GO_TO_TARGET_STATE,
    AUTH_SET_USER_ALREADY_LOGGED_IN, AUTH_SET_USER_NOT_LOGGED_IN, AUTH_SIGN_IN_GOOGLE_LOGIN_ERROR_CORDOVA,
    AUTH_SIGN_IN_GOOGLE_LOGIN_SUCCESS_CORDOVA, AUTH_SIGN_IN_GOOGLE_LOGIN_CORDOVA,
} from "./auth/auth.actions";
import {
    EVENTS_SET_EVENTS,
    EVENTS_SET_SKIP,
    EVENTS_SET_SELECTED_EVENT,
    EVENTS_SHOW_UPCOMING,
    EVENTS_SHOW_PAST,
    EVENTS_RESET_EVENTS_STATE, EVENTS_NOT_LOADING, EVENTS_LOADING, EVENTS_GET_EVENTS_CONTENTFUL,
    EVENTS_GET_EVENTS_CONTENTFUL_SUCCESS, EVENTS_GET_EVENTS_CONTENTFUL_ERROR,
    EVENTS_GET_EVENT_CONTENTFUL, EVENTS_GET_EVENT_CONTENTFUL_ERROR, EVENTS_GET_EVENT_CONTENTFUL_SUCCESS,
    EVENTS_SET_PAST_EVENTS, EVENTS_SET_UPCOMING_EVENTS, EVENTS_GET_UPCOMING_EVENTS_CONTENTFUL,
    EVENTS_GET_PAST_EVENTS_CONTENTFUL,
    EVENTS_GO_TO_SELECTED_EVENT,
} from "./events/events.actions";
import { APP_LOADING, APP_NOT_LOADING } from "./app.actions";
import { GOOGLE_ANALYTICS_LOADING, GOOGLE_ANALYTICS_NOT_LOADING } from "./google-analytics/google-analytics.actions";
import {
    FIREBASE_INIT_WEB, FIREBASE_INIT_WEB_ERROR, FIREBASE_INIT_WEB_SUCCESS, FIREBASE_LOAD_SCRIPT_ERROR,
    FIREBASE_LOAD_SCRIPT_SUCCESS,
    FIREBASE_LOADING,
    FIREBASE_NOT_LOADING, FIREBASE_GLOBAL_READY, FIREBASE_INIT_CORDOVA, FIREBASE_INIT_CORDOVA_SUCCESS,
    FIREBASE_INIT_CORDOVA_ERROR
} from "./firebase/firebase.actions";
import {
    FACEBOOK_DISABLE_LOGIN, FACEBOOK_ENABLE_LOGIN, FACEBOOK_INIT_WEB, FACEBOOK_INIT_WEB_SUCCESS,
    FACEBOOK_LOAD_SCRIPT_ERROR,
    FACEBOOK_LOAD_SCRIPT_SUCCESS,
    FACEBOOK_LOADING,
    FACEBOOK_NOT_LOADING, FACEBOOK_INIT_WEB_ERROR, FACEBOOK_GLOBAL_READY, FACEBOOK_SIGN_IN_OAUTH,
    FACEBOOK_SIGN_IN_OAUTH_SUCCESS, FACEBOOK_SIGN_IN_OAUTH_CORDOVA, FACEBOOK_SIGN_IN_OAUTH_SUCCESS_CORDOVA,
    FACEBOOK_GLOBAL_READY_CORDOVA, FACEBOOK_INIT_CORDOVA,
    FACEBOOK_INIT_CORDOVA_ERROR, FACEBOOK_INIT_CORDOVA_SUCCESS
} from "./facebook/facebook.actions";
import {
    CONTENTFUL_LOADING, CONTENTFUL_NOT_LOADING, CONTENTFUL_RESET_CLIENT,
    CONTENTFUL_SET_CLIENT, CONTENTFUL_INIT_WEB, CONTENTFUL_INIT_WEB_SUCCESS,
    CONTENTFUL_INIT_WEB_ERROR
} from "./contentful/contentful.actions";
import { CORDOVA_INIT, CORDOVA_RESET } from "./cordova/cordova.actions";
import {
    GOOGLE_LOGIN_INIT_WEB, GOOGLE_LOGIN_LOADING,
    GOOGLE_LOGIN_NOT_LOADING, GOOGLE_LOGIN_INIT_WEB_SUCCESS, GOOGLE_LOGIN_INIT_WEB_ERROR, GOOGLE_LOGIN_ENABLE,
    GOOGLE_LOGIN_DISABLE, GOOGLE_LOGIN_OAUTH_ERROR, GOOGLE_LOGIN_OAUTH_SUCCESS, GOOGLE_LOGIN_LOAD_SCRIPT_SUCCESS,
    GOOGLE_LOGIN_LOAD_SCRIPT_ERROR, GOOGLE_LOGIN_GLOBAL_READY, GOOGLE_LOGIN_AUTH2_READY, GOOGLE_LOGIN_OAUTH,
    GOOGLE_LOGIN_INIT_CORDOVA, GOOGLE_LOGIN_INIT_CORDOVA_SUCCESS, GOOGLE_LOGIN_INIT_CORDOVA_ERROR,
    GOOGLE_LOGIN_PLUGIN_READY,
} from "./google-login/google-login.actions";
import {
    DEVICE_SET_BADGE_NUMBER, DEVICE_SET_DEVICE_TOKEN, DEVICE_SET_DEVICE_TYPE,
    DEVICE_SET_PLATFORM
} from "./device/device.actions";

import { authReducer } from "./auth/auth.reducer";
import { eventsReducer } from "./events/events.reducer";
import { googleAnalyticsReducer } from "./google-analytics/google-analytics.reducer";
import { firebaseReducer } from "./firebase/firebase.reducer";
import { facebookReducer } from "./facebook/facebook.reducer";
import { contentfulReducer } from "./contentful/contentful.reducer";
import { cordovaReducer } from "./cordova/cordova.reducer";
import { deviceReducer } from "./device/device.reducer";
import { googleLoginReducer } from "./google-login/google-login.reducer";


const INITIAL_APP_STATE = new AppState();

export function appStateReducer(state: AppState = INITIAL_APP_STATE, action: ActionWPayload) {
    switch (action.type) {

        case APP_LOADING: {
            return {...state, loading: true};
        }
        case APP_NOT_LOADING: {
            return {...state, loading: false};
        }

        case AUTH_SET_USER:
        case AUTH_SET_USER_ALREADY_LOGGED_IN:
        case AUTH_SET_USER_NOT_LOGGED_IN:
        case AUTH_RESET_USER:
        case AUTH_SET_TARGET_STATE:
        case AUTH_RESET_TARGET_STATE:
        case AUTH_GO_TO_TARGET_STATE:
        case AUTH_SIGN_OUT:
        case AUTH_SIGN_OUT_SUCCESS:
        case AUTH_SIGN_OUT_ERROR:
        case AUTH_SIGN_UP_EMAIL:
        case AUTH_SIGN_UP_EMAIL_SUCCESS:
        case AUTH_SIGN_UP_EMAIL_ERROR:
        case AUTH_RESET_PASSWORD:
        case AUTH_RESET_PASSWORD_SUCCESS:
        case AUTH_RESET_PASSWORD_ERROR:
        case AUTH_SIGN_IN_ADD_INVALID_EMAIL:
        case AUTH_SIGN_IN_REMOVE_INVALID_EMAIL:
        case AUTH_SIGN_IN_RESET_INVALID_EMAILS:
        case AUTH_SIGN_IN_FACEBOOK:
        case AUTH_SIGN_IN_FACEBOOK_CORDOVA:
        case AUTH_SIGN_IN_FACEBOOK_WITH_TOKEN:
        case AUTH_SIGN_IN_FACEBOOK_WITH_TOKEN_CORDOVA:
        case AUTH_SIGN_IN_GOOGLE_LOGIN_SUCCESS:
        case AUTH_SIGN_IN_GOOGLE_LOGIN_ERROR:
        case AUTH_SIGN_IN_GOOGLE_LOGIN:
        case AUTH_SIGN_IN_GOOGLE_LOGIN_CORDOVA:
        case AUTH_SIGN_IN_GOOGLE_LOGIN_SUCCESS_CORDOVA:
        case AUTH_SIGN_IN_GOOGLE_LOGIN_ERROR_CORDOVA:
        case AUTH_SIGN_IN_GOOGLE_WITH_TOKEN:
        case AUTH_SIGN_IN_FACEBOOK_SUCCESS:
        case AUTH_SIGN_IN_FACEBOOK_CANCELLED_CORDOVA:
        case AUTH_SIGN_IN_FACEBOOK_ERROR_CORDOVA:
        case AUTH_SIGN_IN_FACEBOOK_SUCCESS_CORDOVA:
        case AUTH_SIGN_IN_FACEBOOK_ERROR:
        case AUTH_SIGN_IN_EMAIL_OTHER_ERROR:
        case AUTH_SIGN_IN_EMAIL_ERROR_WRONG_PASSWORD:
        case AUTH_SIGN_IN_EMAIL: {
            return {...state, auth: authReducer(state.auth, action)};
        }

        case EVENTS_GET_EVENT_CONTENTFUL:
        case EVENTS_GET_EVENT_CONTENTFUL_ERROR:
        case EVENTS_GET_EVENT_CONTENTFUL_SUCCESS:
        case EVENTS_GET_EVENTS_CONTENTFUL:
        case EVENTS_GET_UPCOMING_EVENTS_CONTENTFUL:
        case EVENTS_GET_PAST_EVENTS_CONTENTFUL:
        case EVENTS_GET_EVENTS_CONTENTFUL_ERROR:
        case EVENTS_GO_TO_SELECTED_EVENT:
        case EVENTS_GET_EVENTS_CONTENTFUL_SUCCESS:
        case EVENTS_SET_EVENTS:
        case EVENTS_SET_PAST_EVENTS:
        case EVENTS_SET_UPCOMING_EVENTS:
        case EVENTS_LOADING:
        case EVENTS_NOT_LOADING:
        case EVENTS_RESET_EVENTS_STATE:
        case EVENTS_SET_SKIP:
        case EVENTS_SHOW_UPCOMING:
        case EVENTS_SET_SELECTED_EVENT:
        case EVENTS_SHOW_PAST: {
            return {...state, events: eventsReducer(state.events, action)};
        }

        case GOOGLE_ANALYTICS_LOADING:
        case GOOGLE_ANALYTICS_NOT_LOADING: {
            return {...state, googleAnalyticsLoading: googleAnalyticsReducer(state.googleAnalytics, action)};
        }

        case FIREBASE_LOADING:
        case FIREBASE_INIT_WEB:
        case FIREBASE_INIT_CORDOVA:
        case FIREBASE_INIT_WEB_SUCCESS:
        case FIREBASE_INIT_CORDOVA_SUCCESS:
        case FIREBASE_INIT_WEB_ERROR:
        case FIREBASE_INIT_CORDOVA_ERROR:
        case FIREBASE_LOAD_SCRIPT_SUCCESS:
        case FIREBASE_LOAD_SCRIPT_ERROR:
        case FIREBASE_GLOBAL_READY:
        case FIREBASE_NOT_LOADING: {
            return {...state, firebase: firebaseReducer(state.firebase, action)};
        }

        case FACEBOOK_LOADING:
        case FACEBOOK_NOT_LOADING:
        case FACEBOOK_LOAD_SCRIPT_SUCCESS:
        case FACEBOOK_LOAD_SCRIPT_ERROR:
        case FACEBOOK_INIT_WEB:
        case FACEBOOK_INIT_WEB_SUCCESS:
        case FACEBOOK_INIT_WEB_ERROR:
        case FACEBOOK_INIT_CORDOVA:
        case FACEBOOK_INIT_CORDOVA_SUCCESS:
        case FACEBOOK_INIT_CORDOVA_ERROR:
        case FACEBOOK_SIGN_IN_OAUTH:
        case FACEBOOK_SIGN_IN_OAUTH_CORDOVA:
        case FACEBOOK_SIGN_IN_OAUTH_SUCCESS:
        case FACEBOOK_SIGN_IN_OAUTH_SUCCESS_CORDOVA:
        case FACEBOOK_GLOBAL_READY:
        case FACEBOOK_GLOBAL_READY_CORDOVA:
        case FACEBOOK_DISABLE_LOGIN:
        case FACEBOOK_ENABLE_LOGIN: {
            return {...state, facebook: facebookReducer(state.facebook, action)};
        }

        case GOOGLE_LOGIN_LOADING:
        case GOOGLE_LOGIN_GLOBAL_READY:
        case GOOGLE_LOGIN_PLUGIN_READY:
        case GOOGLE_LOGIN_AUTH2_READY:
        case GOOGLE_LOGIN_NOT_LOADING:
        case GOOGLE_LOGIN_LOAD_SCRIPT_SUCCESS:
        case GOOGLE_LOGIN_LOAD_SCRIPT_ERROR:
        case GOOGLE_LOGIN_INIT_WEB:
        case GOOGLE_LOGIN_INIT_WEB_SUCCESS:
        case GOOGLE_LOGIN_INIT_WEB_ERROR:
        case GOOGLE_LOGIN_INIT_CORDOVA:
        case GOOGLE_LOGIN_INIT_CORDOVA_SUCCESS:
        case GOOGLE_LOGIN_INIT_CORDOVA_ERROR:
        case GOOGLE_LOGIN_OAUTH:
        case GOOGLE_LOGIN_OAUTH_SUCCESS:
        case GOOGLE_LOGIN_OAUTH_ERROR:
        case GOOGLE_LOGIN_DISABLE:
        case GOOGLE_LOGIN_ENABLE: {
            return {...state, googleLogin: googleLoginReducer(state.googleLogin, action)};
        }

        case CONTENTFUL_INIT_WEB:
        case CONTENTFUL_INIT_WEB_SUCCESS:
        case CONTENTFUL_INIT_WEB_ERROR:
        case CONTENTFUL_NOT_LOADING:
        case CONTENTFUL_SET_CLIENT:
        case CONTENTFUL_RESET_CLIENT: {
            return {...state, contentful: contentfulReducer(state.contentful, action)};
        }

        case DEVICE_SET_PLATFORM:
        case DEVICE_SET_DEVICE_TYPE:
        case DEVICE_SET_DEVICE_TOKEN:
        case DEVICE_SET_BADGE_NUMBER: {
            return {...state, device: deviceReducer(state.device, action)};
        }

        case CORDOVA_INIT:
        case CORDOVA_RESET: {
            return {...state, cordova: cordovaReducer(state.cordova, action)};
        }
    }

    // for initial
    return {...state};
}







