import INgModelController = angular.INgModelController;
import ILocationService = angular.ILocationService;
import IFormController = angular.IFormController;

import { BaseController } from "../../../base.controller";
import { TransitionService } from "ui-router-core";
import { NavigationService } from "../../../services/todo-convert-to-pure/navigation.service";
import { LoggingService } from "../../../services/pure/logging.service";
import { INgRedux } from "ng-redux";
import { FirebaseService } from "../../../services/pure/firebase.service";
import { FacebookService } from "../../../services/pure/facebook.service";

import { appLoading } from "../../../redux/app.action-creators";
import {
    auth_signInEmail,
    auth_signInFacebook,
    auth_signInFacebookCordova,
    auth_signInGoogleLogin,
    auth_signInResetInvalidEmails,
    auth_signInGoogleLoginCordova
}from "../../../redux/auth/auth.action-creators";
import { facebookLoading, facebookSignInOAuth } from "../../../redux/facebook/facebook.action-creators";
import { googleLoginLoading, googleLoginOAuth } from "../../../redux/google-login/google-login.action-creators";

export class AuthLoginController extends BaseController {

    email: string;
    password: string;

    constructor($scope: angular.IScope,
                $timeout: angular.ITimeoutService,
                $ngRedux: INgRedux,
                $transitions: TransitionService,
                public navigationService: NavigationService,
                public loggingService: LoggingService,
                public firebaseService: FirebaseService,
                public facebookService: FacebookService,
                public $location: ILocationService,
                public $element: JQuery) {
        super($scope, $timeout, $ngRedux, $transitions);
    }

    $onInit() {
        this.$ngRedux.dispatch(auth_signInResetInvalidEmails());
        this.handleRouteRedirect();

        this.watchForInvalidPasswordAttempts();
    }

    // cordova & web compatible
    signInWithEmailAndPassword(email, password) {
        this.$element.find('#auth-login-email__form__email').blur();
        this.$element.find('#auth-login-email__form__password').blur();

        if (this.$scope.loginForm.$invalid) {
            return;
        }

        this.$ngRedux.dispatch(appLoading());
        this.$timeout(() => {
            this.$ngRedux.dispatch(auth_signInEmail(email, password));
        }, 100);
    }

    signInFB() {
        this.$ngRedux.dispatch(facebookLoading());
        this.$timeout(() => {
            if (this.$ngRedux.getState().app.device.platform === 'cordova') {
                this.$ngRedux.dispatch(auth_signInFacebookCordova());
            } else {
                this.$ngRedux.dispatch(auth_signInFacebook());
            }
        }, 100);
    }

    signInGoogle() {
        this.$ngRedux.dispatch(googleLoginLoading());
        this.$timeout(() => {
            if (this.$ngRedux.getState().app.device.platform === 'cordova') {
                this.$ngRedux.dispatch(auth_signInGoogleLoginCordova());
            } else {
                this.$ngRedux.dispatch(auth_signInGoogleLogin());
            }
        }, 100);
    }

    go(state: string, email: INgModelController, backState?: string) {
        this.$scope.stateGo(state, {
            email: email && email.$viewValue ? email.$viewValue : '',
            backState: backState
        });
    }

    watchForInvalidPasswordAttempts() {
        this.$scope.$watch('state.app.auth.invalidPasswordAttempts', (newValue: any, oldValue: any) => {
            if (newValue > oldValue) {
                this.password = '';
            }
        }, false);
    }

    //================================================================
    // HELPER METHODS
    //================================================================

    handleRouteRedirect() {
        if (this.getFBOAuthRedirectError()) {
            this.handleFBOAuthRedirectError();

        } else if (this.getGoogleLoginOAuthRedirectError()) {
            this.handleGoogleLoginOAuthRedirectError();

        } else if (this.validateFBOAuthToken()) {
            this.cleanURLHash();
            this.handleFBOAuthRedirectSuccess();

        } else if (this.validateGoogleLoginOAuthToken()) {
            // no need to clear the token in the has,
            // the iframe does it automatically
            this.handleGoogleLoginOAuthRedirectSuccess();
        }
    }

    getFBOAuthRedirectError() {
        let error = this.$scope.state.router.currentParams['error'];
        if (error && error.length) {
            return error;
        }
        return null;
    }

    handleFBOAuthRedirectError() {
        this.loggingService.log({
            level: 2,
            entity: AuthLoginController,
            message: 'Facebook login was denied or cancelled.',
            displayOnScreen: true,
        });

        this.cleanURLHash();

        this.$timeout(() => {
            this.$scope.stateGo('login', undefined, {
                location: true, reload: false
            });
        })
    }

    getGoogleLoginOAuthRedirectError() {
        let error = this.getURLHash();
        if (error && error.length && error.indexOf('error=') === 0) {
            return error;
        }
        return null;
    }

    handleGoogleLoginOAuthRedirectError() {
        this.loggingService.log({
            level: 2,
            entity: AuthLoginController,
            message: 'Google login was denied or cancelled.',
            displayOnScreen: true,
        });
    }

    // TODO move to redux listener
    handleFBOAuthRedirectSuccess() {
        this.$scope.$watch('state.app.facebook.facebook', ((facebook: boolean) => {
            if (facebook) {
                this.$ngRedux.dispatch(appLoading());
                this.$ngRedux.dispatch(facebookSignInOAuth());
            }
        }), false)
    }

    validateFBOAuthToken() {
        let hash = this.getURLHash();
        if (hash && hash.length) {
            var access_token = hash.match(/access_token=[A-Z,a-z,0-9,_]*/);
            if (access_token && access_token.length) {
                return true;
            }
        }
        return false;
    }

    // TODO move to redux listener
    handleGoogleLoginOAuthRedirectSuccess() {
        this.$scope.$watch('state.app.googleLogin.auth2Ready', ((auth2Ready: boolean) => {
            if (auth2Ready) {
                this.$ngRedux.dispatch(appLoading());
                this.$ngRedux.dispatch(googleLoginOAuth());
            }
        }), false)
    }

    validateGoogleLoginOAuthToken() {
        let hash = this.getURLHash();
        if (hash && hash.length) {
            var id_token = hash.match(/id_token=[A-Z,a-z,0-9,_,@,=]*/);
            if (id_token && id_token.length) {
                return true;
            }
        }
        return false;
    }

    getURLHash() {
        let paramsHash = this.$scope.state.router.currentParams['#'];

        if (paramsHash && paramsHash.length) {
            return paramsHash;
        }

        return this.$location.hash();
    }

    cleanURLHash() {
        this.$location.hash('');
    }
}

AuthLoginController.$inject = [
    ...BaseController.injects,
    'navigationService',
    'loggingService',
    'firebaseService',
    'facebookService',
    '$location',
    '$element'
];


