import IModule = angular.IModule;

declare var angular: angular.IAngularStatic;

import { ValidateEmailValidator } from "./validators/validate-email.directive";
import { EmailWithDotValidatorDirective } from "./validators/email-w-dot-validator.directive";
import { NotEqualToValidator } from "./validators/not-equal-to-validator.directive";
import { OnFocusSelectDirective } from "./select-on-focus.directive";
import { FakeActionDirective } from "./fake-action.directive";

export let HelperModule: IModule = angular.module('helpers', []);

HelperModule.directive('onFocusSelect', OnFocusSelectDirective);
HelperModule.directive('notEqualTo', NotEqualToValidator);
HelperModule.directive('validateEmail', ValidateEmailValidator);
HelperModule.directive('emailWithDot', EmailWithDotValidatorDirective);
HelperModule.directive('fakeAction', FakeActionDirective);




