import { FirebaseUserModel } from "../models/firebase-user.model";
import { TargetState } from "angular-ui-router";

export class AppState {
    public loading: boolean = true;

    public auth: AuthState = new AuthState();
    public firebase: FirebaseState = new FirebaseState();
    public facebook: FacebookState = new FacebookState();
    public googleLogin: GoogleLoginState = new GoogleLoginState();
    public googleAnalytics: GoogleAnalyticsState = new GoogleAnalyticsState();
    public contentful: ContentfulState = new ContentfulState();
    public cordova: CordovaState = new CordovaState();
    public events: EventsState = new EventsState();
    public device: DeviceState = new DeviceState();

    // TODO persistent caching, settings etc
}

export class AuthState {
    public user: FirebaseUserModel = undefined;
    public userAlreadyLoggedIn: boolean = false;
    public signingOut: boolean = false;
    public invalidEmails: string[] = [];
    public invalidPasswordAttempts: number = 0;
    public targetState: TargetState;
}

export class EventsState {
    public events: any[] = [];
    public displayedEvents: any[] = [];
    public upcomingEvents: any[] = [];
    public pastEvents: any[] = [];
    public eventsLoading: boolean = false;
    public eventsShowUpcoming: boolean = true;
    public eventsShowPast: boolean = false;
    public eventsSkip: number = 0;
    public selectedEvent: any = null;
}

export class FirebaseState {
    public firebaseReady: boolean = false;
    public firebaseLoading: boolean = true;
}

export class ContentfulState {
    public contentfulClient: any = null;
    public contentfulLoading: boolean;
}

export class GoogleAnalyticsState {
    public googleAnalyticsLoading: boolean = false;
}

export class GoogleLoginState {
    public googleLoginLoading: boolean = true;
    public googleLoginDisabled: boolean = true;
    public gapiReady: boolean = false;
    public auth2Ready: boolean = false;
    public pluginReady: boolean = false;
}

export class FacebookState {
    public facebook: boolean = false;
    public facebookCordovaPlugin: boolean = false;
    public facebookLoading: boolean = true;
    public facebookLoginDisabled: boolean = false;
}

export class DeviceState {
    public badgeNumber: number = null;
    public deviceToken: string = null;
    public platform: string = ''; // web, cordova
    public deviceType: string = ''; // ios, android
}

export class CordovaState {
    public cordova: any = null;
}





