import IFilterService = angular.IFilterService;
import IQService = angular.IQService;

import { Subject } from 'rxjs/Subject';
import { LogEventModel } from "../../models/log-event.model";
import { OnScreenNotificationModel } from "../../models/on-screen-notification.model";

export class OnScreenNotificationsService {

    private counter: number = 0;

    private onScreenNotificationsSource = new Subject<OnScreenNotificationModel>();
    onScreenNotificationsQueue$ = this.onScreenNotificationsSource.asObservable();

    onScreenNotificationssToBeDeleted: OnScreenNotificationModel[] = [];
    onScreenNotificationsToBeDeletedSource = new Subject<OnScreenNotificationModel>();
    onScreenNotificationsToBeDeletedQueue$ = this.onScreenNotificationsToBeDeletedSource.asObservable();

    constructor() {
    }

    publishFromLogEvent(logEvent: LogEventModel) {
        let notif = new OnScreenNotificationModel(logEvent);
        notif.id = this.counter++;
        this.onScreenNotificationsSource.next(notif);
    }

    publish(notification: OnScreenNotificationModel) {
        notification.id = this.counter++;
        this.onScreenNotificationsSource.next(notification);
    }
}

OnScreenNotificationsService.$inject = [];


