import { Action } from "redux";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";

import {
    AUTH_RESET_USER, AUTH_SET_USER, AUTH_SIGN_IN_EMAIL, AUTH_SIGN_IN_EMAIL_SUCCESS,
    AUTH_SIGN_IN_EMAIL_ERROR_INVALID_EMAIL, AUTH_SIGN_IN_EMAIL_ERROR_WRONG_PASSWORD, AUTH_SIGN_IN_RESET_INVALID_EMAILS,
    AUTH_SIGN_IN_ADD_INVALID_EMAIL, AUTH_SIGN_IN_REMOVE_INVALID_EMAIL, AUTH_SIGN_IN_EMAIL_OTHER_ERROR,
    AUTH_SIGN_IN_FACEBOOK, AUTH_SIGN_IN_FACEBOOK_SUCCESS, AUTH_SIGN_IN_FACEBOOK_ERROR, AUTH_SIGN_IN_FACEBOOK_WITH_TOKEN,
    AUTH_SIGN_IN_GOOGLE_LOGIN, AUTH_SIGN_IN_GOOGLE_LOGIN_ERROR, AUTH_SIGN_IN_GOOGLE_LOGIN_SUCCESS,
    AUTH_SIGN_IN_GOOGLE_WITH_TOKEN, AUTH_SIGN_UP_EMAIL, AUTH_SIGN_UP_EMAIL_SUCCESS, AUTH_SIGN_UP_EMAIL_ERROR,
    AUTH_RESET_PASSWORD, AUTH_RESET_PASSWORD_SUCCESS, AUTH_RESET_PASSWORD_ERROR, AUTH_SIGN_OUT, AUTH_SIGN_OUT_ERROR,
    AUTH_SIGN_OUT_SUCCESS, AUTH_SIGN_IN_FACEBOOK_WITH_TOKEN_CORDOVA, AUTH_SIGN_IN_FACEBOOK_SUCCESS_CORDOVA,
    AUTH_SIGN_IN_FACEBOOK_CORDOVA, AUTH_SIGN_IN_FACEBOOK_ERROR_CORDOVA, AUTH_SIGN_IN_FACEBOOK_CANCELLED_CORDOVA,
    AUTH_SET_TARGET_STATE, AUTH_GO_TO_TARGET_STATE, AUTH_SET_USER_ALREADY_LOGGED_IN, AUTH_SET_USER_NOT_LOGGED_IN,
    AUTH_SIGN_IN_GOOGLE_LOGIN_CORDOVA, AUTH_SIGN_IN_GOOGLE_LOGIN_SUCCESS_CORDOVA,
    AUTH_SIGN_IN_GOOGLE_LOGIN_ERROR_CORDOVA, AUTH_SIGN_IN_GOOGLE_WITH_TOKEN_CORDOVA,
} from "./auth.actions";

export function auth_signInEmail(email, password): ActionWPayload {
    return {
        type: AUTH_SIGN_IN_EMAIL,
        payload: {
            email,
            password
        }
    }
}

export function auth_signOut(): Action {
    return {
        type: AUTH_SIGN_OUT,
    }
}

export function auth_signOutSuccess(): Action {
    return {
        type: AUTH_SIGN_OUT_SUCCESS,
    }
}

export function auth_signOutError(): Action {
    return {
        type: AUTH_SIGN_OUT_ERROR,
    }
}

export function auth_signUpEmail(email, password): ActionWPayload {
    return {
        type: AUTH_SIGN_UP_EMAIL,
        payload: {
            email,
            password
        }
    }
}

export function auth_signUpEmailSuccess(): Action {
    return {
        type: AUTH_SIGN_UP_EMAIL_SUCCESS,
    }
}

export function auth_signUpEmailError(error): ActionWPayload {
    return {
        type: AUTH_SIGN_UP_EMAIL_ERROR,
        payload: error
    }
}

export function auth_setTargetState(targetState): ActionWPayload {
    return {
        type: AUTH_SET_TARGET_STATE,
        payload: targetState
    }
}

export function auth_resetTargetState(): Action {
    return {
        type: AUTH_SET_TARGET_STATE
    }
}

export function auth_resetPassword(email): ActionWPayload {
    return {
        type: AUTH_RESET_PASSWORD,
        payload: email
    }
}

export function auth_resetPasswordSuccess(): Action {
    return {
        type: AUTH_RESET_PASSWORD_SUCCESS,
    }
}

export function auth_resetPasswordError(error): ActionWPayload {
    return {
        type: AUTH_RESET_PASSWORD_ERROR,
        payload: error
    }
}

export function auth_signInGoogleLogin(): Action {
    return {
        type: AUTH_SIGN_IN_GOOGLE_LOGIN,
    }
}

export function auth_signInGoogleLoginWToken(GoogleUser): ActionWPayload {
    return {
        type: AUTH_SIGN_IN_GOOGLE_WITH_TOKEN,
        payload: GoogleUser
    }
}

export function auth_signInGoogleLoginSuccess(): Action {
    return {
        type: AUTH_SIGN_IN_GOOGLE_LOGIN_SUCCESS,
    }
}

export function auth_signInGoogleLoginError(error): ActionWPayload {
    return {
        type: AUTH_SIGN_IN_GOOGLE_LOGIN_ERROR,
        payload: error
    }
}

export function auth_signInGoogleLoginCordova(): Action {
    return {
        type: AUTH_SIGN_IN_GOOGLE_LOGIN_CORDOVA,
    }
}

export function auth_signInGoogleLoginWTokenCordova(pluginAuthResponse): ActionWPayload {
    return {
        type: AUTH_SIGN_IN_GOOGLE_WITH_TOKEN_CORDOVA,
        payload: pluginAuthResponse
    }
}

export function auth_signInGoogleLoginSuccessCordova(): Action {
    return {
        type: AUTH_SIGN_IN_GOOGLE_LOGIN_SUCCESS_CORDOVA,
    }
}

export function auth_signInGoogleLoginErrorCordova(error): ActionWPayload {
    return {
        type: AUTH_SIGN_IN_GOOGLE_LOGIN_ERROR_CORDOVA,
        payload: error
    }
}

export function auth_signInFacebook(): Action {
    return {
        type: AUTH_SIGN_IN_FACEBOOK,
    }
}

export function auth_signInFacebookWToken(response): ActionWPayload {
    return {
        type: AUTH_SIGN_IN_FACEBOOK_WITH_TOKEN,
        payload: response
    }
}

export function auth_signInFacebookSuccess(): Action {
    return {
        type: AUTH_SIGN_IN_FACEBOOK_SUCCESS,
    }
}

export function auth_signInFacebookError(error): ActionWPayload {
    return {
        type: AUTH_SIGN_IN_FACEBOOK_ERROR,
        payload: error
    }
}

export function auth_signInFacebookCordova(): Action {
    return {
        type: AUTH_SIGN_IN_FACEBOOK_CORDOVA,
    }
}

export function auth_signInFacebookWTokenCordova(response): ActionWPayload {
    return {
        type: AUTH_SIGN_IN_FACEBOOK_WITH_TOKEN_CORDOVA,
        payload: response
    }
}

export function auth_signInFacebookSuccessCordova(): Action {
    return {
        type: AUTH_SIGN_IN_FACEBOOK_SUCCESS_CORDOVA,
    }
}

export function auth_signInFacebookErrorCordova(error): ActionWPayload {
    return {
        type: AUTH_SIGN_IN_FACEBOOK_ERROR_CORDOVA,
        payload: error
    }
}

export function auth_signInFacebookCancelledCordova(): Action {
    return {
        type: AUTH_SIGN_IN_FACEBOOK_CANCELLED_CORDOVA
    }
}

export function auth_signInEmailSuccess(): Action {
    return {
        type: AUTH_SIGN_IN_EMAIL_SUCCESS,
    }
}

export function auth_signInEmailErrorInvalidEmail(): Action {
    return {
        type: AUTH_SIGN_IN_EMAIL_ERROR_INVALID_EMAIL,
    }
}

export function auth_signInAddInvalidEmail(invalidEmail): ActionWPayload {
    return {
        type: AUTH_SIGN_IN_ADD_INVALID_EMAIL,
        payload: invalidEmail
    }
}

export function auth_signInRemoveInvalidEmail(invalidEmail): ActionWPayload {
    return {
        type: AUTH_SIGN_IN_REMOVE_INVALID_EMAIL,
        payload: invalidEmail
    }
}

export function auth_signInResetInvalidEmails(): Action {
    return {
        type: AUTH_SIGN_IN_RESET_INVALID_EMAILS
    }
}

export function auth_signInEmailErrorWrongPassword(): Action {
    return {
        type: AUTH_SIGN_IN_EMAIL_ERROR_WRONG_PASSWORD,
    }
}

export function auth_signInEmailOtherError(err): ActionWPayload {
    return {
        type: AUTH_SIGN_IN_EMAIL_OTHER_ERROR,
        payload: err
    }
}

export function auth_setUser(user): ActionWPayload {
    return {
        type: AUTH_SET_USER,
        payload: user
    }
}

export function auth_setUserAlreadyLoggedIn(): Action {
    return {
        type: AUTH_SET_USER_ALREADY_LOGGED_IN
    }
}

export function auth_setUserNotLoggedIn(): Action {
    return {
        type: AUTH_SET_USER_NOT_LOGGED_IN
    }
}

export function auth_resetUser(): Action {
    return {
        type: AUTH_RESET_USER
    }
}

export function auth_goToTargetState(): Action {
    return {
        type: AUTH_GO_TO_TARGET_STATE
    }
}


