import { Dispatch } from "redux";

import { injector } from "../../app";

import { IGlobalAppState } from "../../interfaces/global-app-state.interface";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";
import { IListenerMiddleWareListener } from "../app-listener.middleware";
import { ContentfulService } from "../../services/pure/contentful.service";

import { CONTENTFUL_INIT_WEB } from "./contentful.actions";
import { CONTENTFUL_INIT_OPTIONS } from "../../app.constants";

import { appLoading, appNotLoading } from "../app.action-creators";
import {
    contentfulLoading,
    contentfulNotLoading,
    contentfulInitWebError, contentfulSetClient, contentfulInitWebSuccess, contentfulResetClient
} from "./contentful.action-creators";

export let contentfulListener: IListenerMiddleWareListener = {
    [CONTENTFUL_INIT_WEB]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let contentfulService = <ContentfulService>injector.get('contentfulService');

        dispatch(contentfulLoading());

        try {
            if (!contentfulService.getContentfulGlobal()) {
                throw new Error("Contentful is not loaded.");
            } else {
                let contentfulClient = contentfulService.createClient(CONTENTFUL_INIT_OPTIONS);

                if (!contentfulClient) {
                    throw new Error("Contentful init failed.");
                }
                dispatch(contentfulSetClient(contentfulClient));
                dispatch(contentfulInitWebSuccess());
            }
        } catch (error) {
            dispatch(contentfulInitWebError(error));
            dispatch(contentfulResetClient());
        }

        dispatch(contentfulNotLoading());
    },
};


