import IQService = angular.IQService;
import IFilterService = angular.IFilterService;

import { BaseService } from "../base.service";
import { LoggingService } from "./logging.service";
import IDeferred = angular.IDeferred;
import { LogEventModel } from "../../models/log-event.model";

//================================================================
// CORDOVA: https://documentation.onesignal.com/docs/cordova-sdk
// WEB: https://documentation.onesignal.com/docs/web-push-sdk
//================================================================

export class OneSignalService extends BaseService {

    constructor($q: IQService, $filter: IFilterService, loggingService: LoggingService) {
        super($q, $filter, loggingService);
    }

    getOneSignalGlobal() {
        if (window['OneSignal']) {
            return window['OneSignal'];
        }
    }

    //================================================================
    // WEB
    //================================================================

    initWeb() {
        let deferred = this.$q.defer();

        jQuery.getScript("https://cdn.onesignal.com/sdks/OneSignalSDK.js")
            .done((script, textStatus) => {
                this.checkOneSignalIsLoaded(deferred);

                try {
                    this.getOneSignalGlobal().push(["init", {
                        appId: "a4acb3bb-f4db-4e8a-b631-cf1f725e8269",
                        autoRegister: false,
                        notifyButton: {
                            enable: true /* Set to false to hide */
                        },
                        persistNotification: false,
                        safari_web_id: 'web.onesignal.auto.370dd028-ab8a-4720-9087-4e2c917686de'
                    }]);

                    deferred.resolve({
                        level: 1,
                        entity: OneSignalService,
                        message: 'OneSignal loaded successfully.'
                    });
                } catch (err) {
                    deferred.reject({
                        level: 3,
                        entity: OneSignalService,
                        message: 'OneSignal init failed.'
                    });
                }
            })
            .fail((jqxhr, settings, exception) => {
                deferred.reject({
                    level: 2,
                    entity: OneSignalService,
                    message: 'OneSignal not be loaded.'
                });
            });

        return deferred.promise;
    }

    private checkOneSignalIsLoaded(deferred: IDeferred<LogEventModel>) {
        if (!this.getOneSignalGlobal()) {
            deferred.reject({
                level: 3,
                entity: OneSignalService,
                message: 'OneSignal could not be loaded.'
            });
        }
    }

    //================================================================
    // CORDOVA
    //================================================================

    initCordova() {

        // document.addEventListener('deviceready', function () {
        //     // Enable to debug issues.
        //     // window.plugins.OneSignal.setLogLevel({logLevel: 4, visualLevel: 4});
        //
        //     var notificationOpenedCallback = function(jsonData) {
        //         console.log('notificationOpenedCallback: ' + JSON.stringify(jsonData));
        //     };
        //
        //     window.plugins.OneSignal
        //         .startInit("YOUR_APPID")
        //         .handleNotificationOpened(notificationOpenedCallback)
        //         .endInit();
        //
        //     // Call syncHashedEmail anywhere in your app if you have the auth's email.
        //     // This improves the effectiveness of OneSignal's "best-time" notification scheduling feature.
        //     // window.plugins.OneSignal.syncHashedEmail(userEmail);
        // }, false);

        // window.plugins.OneSignal
        //     .startInit("a4acb3bb-f4db-4e8a-b631-cf1f725e8269")
        //     .endInit();
    }
}

OneSignalService.$inject = [...BaseService.injects];



