import { Action } from "redux";
import { GOOGLE_ANALYTICS_LOADING, GOOGLE_ANALYTICS_NOT_LOADING } from "./google-analytics.actions";

export function googleAnalyticsLoading(): Action {
    return {
        type: GOOGLE_ANALYTICS_LOADING
    }
}
export function googleAnalyticsNotLoading(): Action {
    return {
        type: GOOGLE_ANALYTICS_NOT_LOADING
    }
}


