import IModule = angular.IModule;

declare var angular: angular.IAngularStatic;

import { OnScreenNotificationsAnimation } from './notifications/on-screen-notifications.animation';
import { InputValidationAnimation } from "./input-validation/input-validation.animation";

import { OnScreenNotificationsComponent } from './notifications/on-screen-notifications.component';
import { InputWrapperComponent } from "./input-wrapper/input-wrapper.component";

import { InputValidationDirective } from "./input-validation/input-validation.directive";
import { LoaderDirective } from "./loader/loader.directive";

export let UIComponentsModule: IModule = angular.module('app.ui-components', [
    'ngAnimate'
]);

UIComponentsModule.animation('.on-screen-notifications__animate', OnScreenNotificationsAnimation);
UIComponentsModule.animation('.input-validation__animate', InputValidationAnimation);

UIComponentsModule.component('onScreenNotifications', OnScreenNotificationsComponent);
UIComponentsModule.component('inputWrapper', InputWrapperComponent);

UIComponentsModule.directive('inputValidation', InputValidationDirective);
UIComponentsModule.directive('loader', LoaderDirective);



