import IModule = angular.IModule;
import ILocationProvider = angular.ILocationProvider;

import { StateProvider, UIRouter } from "angular-ui-router";
import { UrlRouterProvider } from "angular-ui-router/lib/urlRouterProvider";

import { INgReduxProvider } from "ng-redux";
import { combineReducers } from 'redux';
import thunk from 'redux-thunk';
import { createLogger } from 'redux-logger';
import { router } from 'redux-ui-router/lib/index.js';

import { StickyStatesPlugin } from "ui-router-sticky-states";

import { LOCAL_STORAGE_PREFIX, REDUX_LOGGER_OPTIONS } from "./app.constants";

import { appStateReducer } from "./redux/app.reducers";
import { listenerMiddleWare } from "./redux/app-listener.middleware";

import { authListener } from "./redux/auth/auth.listeners";
import { loggingListeners } from "./redux/logging/logging.listeners";
import { firebaseListener } from "./redux/firebase/firebase.listeners";
import { facebookListener } from "./redux/facebook/facebook.listeners";
import { googleLoginListener } from "./redux/google-login/google-login.listeners";
import { contentfulListener } from "./redux/contentful/contentful.listeners";
import { eventsListener } from "./redux/events/events.listeners";

export function AppConfig($stateProvider: StateProvider,
                          $uiRouterProvider: UIRouter,
                          $urlRouterProvider: UrlRouterProvider,
                          $locationProvider: ILocationProvider,
                          localStorageServiceProvider,
                          $ngReduxProvider: INgReduxProvider,
                          $uiViewScrollProvider) {

    //================================================================
    // LOCATION
    //================================================================

    // @see: https://github.com/angular-ui/ui-router/wiki/Frequently-Asked-Questions
    // #how-to-configure-your-server-to-work-with-html5mode
    // $locationProvider.html5Mode(true).hashPrefix('#');

    if (window['cordova']) {
        $locationProvider.html5Mode({
            enabled: false,
            requireBase: false,
            rewriteLinks: false
        });
    } else {
        $locationProvider.html5Mode({
            enabled: true,
            requireBase: true,
            rewriteLinks: true
        });
    }

    $locationProvider.hashPrefix('!');

    //================================================================
    // UI ROUTER
    //================================================================    

    $stateProvider.state('app', {
        url: '/',
        redirectTo: 'login'
    });

    $uiViewScrollProvider.useAnchorScroll(); // TODO.. still needed ?

    $uiRouterProvider.plugin(StickyStatesPlugin);


    //================================================================
    // URL
    //================================================================

    $urlRouterProvider.otherwise('/');

    //================================================================
    // LOCAL STORAGE
    //================================================================    

    localStorageServiceProvider.setPrefix(LOCAL_STORAGE_PREFIX);

    //================================================================
    // REDUX AND REDUX-UI-ROUTER
    //================================================================

    const logger = createLogger(REDUX_LOGGER_OPTIONS);

    // *** example of a simple middleware

    // const logger = store => next => action => {
    //     console.log(action.type, action);
    //     return next(action);
    //     // console.log('next state', store.getState())
    // };

    // ***

    const reducers = combineReducers({
        router: router,
        app: appStateReducer,
    });

    const listeners = [
        eventsListener,
        contentfulListener,
        googleLoginListener,
        facebookListener,
        firebaseListener,
        authListener,
        loggingListeners
    ];

    const middlewares = ['ngUiRouterMiddleware', thunk, listenerMiddleWare(listeners), logger];

    const enhancers = [];

    // *** for redux dev tool (chrome extension)

    if (window['__REDUX_DEVTOOLS_EXTENSION_COMPOSE__']) {
        const DevToolCompose = window['__REDUX_DEVTOOLS_EXTENSION_COMPOSE__'];
        enhancers.push(DevToolCompose())
    }

    $ngReduxProvider.createStoreWith(reducers, middlewares, enhancers);
}

AppConfig.$inject = [
    '$stateProvider',
    '$uiRouterProvider',
    '$urlRouterProvider',
    '$locationProvider',
    'localStorageServiceProvider',
    '$ngReduxProvider',
    '$uiViewScrollProvider'
];













