import { Action } from "redux";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";

import {
    EVENTS_SET_SELECTED_EVENT, EVENTS_SET_EVENTS, EVENTS_SET_SKIP, EVENTS_SHOW_PAST,
    EVENTS_SHOW_UPCOMING, EVENTS_RESET_EVENTS_STATE, EVENTS_GET_EVENTS_CONTENTFUL, EVENTS_LOADING, EVENTS_NOT_LOADING,
    EVENTS_GET_EVENTS_CONTENTFUL_SUCCESS, EVENTS_GET_EVENTS_CONTENTFUL_ERROR, EVENTS_GET_EVENT_CONTENTFUL,
    EVENTS_GET_EVENT_CONTENTFUL_ERROR, EVENTS_GET_EVENT_CONTENTFUL_SUCCESS, EVENTS_SET_UPCOMING_EVENTS,
    EVENTS_SET_PAST_EVENTS, EVENTS_GET_UPCOMING_EVENTS_CONTENTFUL, EVENTS_GET_PAST_EVENTS_CONTENTFUL,
    EVENTS_GO_TO_SELECTED_EVENT
} from "./events.actions";

export function events_loading(): Action {
    return {
        type: EVENTS_LOADING,
    }
}

export function events_notLoading(): Action {
    return {
        type: EVENTS_NOT_LOADING,
    }
}

export function events_setEvents(events): ActionWPayload {
    return {
        type: EVENTS_SET_EVENTS,
        payload: events
    }
}

export function events_setUpcomingEvents(upcomingEvents): ActionWPayload {
    return {
        type: EVENTS_SET_UPCOMING_EVENTS,
        payload: upcomingEvents
    }
}

export function events_setPastEvents(pastEvents): ActionWPayload {
    return {
        type: EVENTS_SET_PAST_EVENTS,
        payload: pastEvents
    }
}

export function events_getEventsFromContentful(skip, limit): ActionWPayload {
    return {
        type: EVENTS_GET_EVENTS_CONTENTFUL,
        payload: {
            skip,
            limit
        }
    }
}

export function events_getUpcomingEventsFromContentful(): Action {
    return {
        type: EVENTS_GET_UPCOMING_EVENTS_CONTENTFUL,
    }
}

export function events_getPastEventsFromContentful(): Action {
    return {
        type: EVENTS_GET_PAST_EVENTS_CONTENTFUL,
    }
}

export function events_getEventsFromContentfulSuccess(): Action {
    return {
        type: EVENTS_GET_EVENTS_CONTENTFUL_SUCCESS,
    }
}

export function events_getEventsFromContentfulError(error): ActionWPayload {
    return {
        type: EVENTS_GET_EVENTS_CONTENTFUL_ERROR,
        payload: error
    }
}

export function events_getEventFromContentful(eventId): ActionWPayload {
    return {
        type: EVENTS_GET_EVENT_CONTENTFUL,
        payload: eventId
    }
}

export function events_getEventFromContentfulSuccess(): Action {
    return {
        type: EVENTS_GET_EVENT_CONTENTFUL_SUCCESS,
    }
}

export function events_getEventFromContentfulError(error): ActionWPayload {
    return {
        type: EVENTS_GET_EVENT_CONTENTFUL_ERROR,
        payload: error
    }
}

export function events_resetEventsState(): Action {
    return {
        type: EVENTS_RESET_EVENTS_STATE
    }
}

export function events_showUpcomingEvents(): Action {
    return {
        type: EVENTS_SHOW_UPCOMING
    }
}

export function events_showPastEvents(): Action {
    return {
        type: EVENTS_SHOW_PAST
    }
}

export function events_setSkip(number): ActionWPayload {
    return {
        type: EVENTS_SET_SKIP,
        payload: number
    }
}

export function events_setSelectedEvent(event): ActionWPayload {
    return {
        type: EVENTS_SET_SELECTED_EVENT,
        payload: event
    }
}

export function events_goToSelectedEvent(): Action {
    return {
        type: EVENTS_GO_TO_SELECTED_EVENT
    }
}


