import { Dispatch } from "redux";
import { TargetState } from "angular-ui-router/lib";

import { stateGo } from "redux-ui-router";

import { injector } from "../../app";
import { IGlobalAppState } from "../../interfaces/global-app-state.interface";
import { FirebaseService } from "../../services/pure/firebase.service";
import { IListenerMiddleWareListener } from "../app-listener.middleware";
import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";

import {
    AUTH_RESET_PASSWORD,
    AUTH_SIGN_IN_EMAIL, AUTH_SIGN_IN_FACEBOOK, AUTH_SIGN_IN_FACEBOOK_CORDOVA, AUTH_SIGN_IN_FACEBOOK_WITH_TOKEN,
    AUTH_SIGN_IN_FACEBOOK_WITH_TOKEN_CORDOVA,
    AUTH_SIGN_IN_GOOGLE_LOGIN,
    AUTH_SIGN_IN_GOOGLE_WITH_TOKEN, AUTH_SIGN_OUT, AUTH_SIGN_UP_EMAIL, AUTH_GO_TO_TARGET_STATE, AUTH_SET_USER,
    AUTH_SIGN_IN_GOOGLE_LOGIN_CORDOVA, AUTH_SIGN_IN_GOOGLE_WITH_TOKEN_CORDOVA
} from "./auth.actions";

import { facebookNotLoading } from "../facebook/facebook.action-creators";
import { googleLoginNotLoading } from "../google-login/google-login.action-creators";
import { appLoading, appNotLoading } from "../app.action-creators";
import {
    auth_goToTargetState,
    auth_resetPasswordError,
    auth_resetPasswordSuccess,
    auth_resetUser, auth_setUser, auth_setUserAlreadyLoggedIn, auth_signInAddInvalidEmail,
    auth_signInEmailErrorInvalidEmail,
    auth_signInEmailErrorWrongPassword, auth_signInEmailOtherError,
    auth_signInEmailSuccess, auth_signInFacebookCancelledCordova, auth_signInFacebookCordova,
    auth_signInFacebookError,
    auth_signInFacebookErrorCordova, auth_signInFacebookSuccess,
    auth_signInFacebookSuccessCordova,
    auth_signInGoogleLoginError, auth_signInGoogleLoginErrorCordova,
    auth_signInGoogleLoginSuccess, auth_signInGoogleLoginSuccessCordova, auth_signOut, auth_signOutSuccess,
    auth_signUpEmailError,
    auth_signUpEmailSuccess
} from "./auth.action-creators";
import { events_resetEventsState } from "../events/events.action-creators";
import { GoogleLoginAuthResponseModel } from "../../models/google-login-auth-user-response.model";
import { GoogleLoginService } from "../../services/pure/google-login.service";
import { FacebookService } from "../../services/pure/facebook.service";
import { GoogleLoginPluginAuthResposeModel } from "../../models/google-login-plugin-auth-response.model";

export let authListener: IListenerMiddleWareListener = {
    [AUTH_SIGN_OUT]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let firebaseService = <FirebaseService>injector.get('firebaseService');
        let googleLoginService = <GoogleLoginService>injector.get('googleLoginService');
        let facebookService = <FacebookService>injector.get('facebookService');

        dispatch(appLoading());

        firebaseService.signOut();

        //================================================================
        // WEB
        //================================================================

        if (state.app.device.platform === 'web') {
            googleLoginService.signOut()
                .catch(error => {
                    // normal to get error in dev mode
                });
        }

        //================================================================
        // CORDOVA
        //================================================================

        if (state.app.device.platform === 'cordova') {
            facebookService.signOutCordova();
            googleLoginService.signOutCordova(response => {
            })
        }

        //
        // Clean up
        //

        dispatch(events_resetEventsState());
        dispatch(auth_resetUser());
        dispatch(auth_signOutSuccess());

        setTimeout(() => {
            dispatch(stateGo('login', {
                comingFromBackButton: true
            }));
            dispatch(appNotLoading());
        }, 1000)
    },
    [AUTH_SIGN_UP_EMAIL]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let firebaseService = <FirebaseService>injector.get('firebaseService');

        dispatch(appLoading());

        firebaseService.createUserWithEmailAndPassword(action.payload.email, action.payload.password)
            .then(user => {
                dispatch(auth_signUpEmailSuccess());
                dispatch(auth_setUser(user));
                dispatch(auth_goToTargetState());
                setTimeout(() => {
                    dispatch(auth_setUserAlreadyLoggedIn());
                }, 1000);
            })
            .catch(error => {
                switch (error.code) {
                    case 'auth/email-already-in-use':
                        dispatch(auth_signInAddInvalidEmail(action.payload.email));
                        break;
                }
                dispatch(auth_signUpEmailError(error));
                dispatch(auth_resetUser());

            })
            .finally(() => {
                dispatch(appNotLoading());
            });
    },
    [AUTH_RESET_PASSWORD]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let firebaseService = <FirebaseService>injector.get('firebaseService');

        dispatch(appLoading());

        firebaseService.resetPassword(action.payload)
            .then(result => {
                dispatch(auth_resetPasswordSuccess());
            })
            .catch(error => {
                switch (error.code) {
                    case 'auth/user-not-found':
                    case 'auth/user-disabled':
                    case 'auth/invalid-email': {
                        error.message = 'Invalid email.';
                        dispatch(auth_resetPasswordError(error));
                        dispatch(auth_signInAddInvalidEmail(action.payload));
                        break;
                    }

                    default:
                        dispatch(auth_resetPasswordError(error));
                        break;
                }
            })
            .finally(() => {
                dispatch(appNotLoading());
            })
    },
    [AUTH_SIGN_IN_EMAIL]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {

        let firebaseService = <FirebaseService>injector.get('firebaseService');

        firebaseService.signInWithEmailAndPassword(action.payload.email, action.payload.password)
            .then(user => {
                if (user) {
                    dispatch(auth_signInEmailSuccess());
                    dispatch(auth_setUser(user));
                    dispatch(auth_goToTargetState());
                    setTimeout(() => {
                        dispatch(auth_setUserAlreadyLoggedIn());
                    }, 1000);
                } else {
                    dispatch(auth_signInEmailOtherError(new Error('Invalid user returned')));
                }
            })
            .catch(error => {
                switch (error.code) {
                    case 'auth/user-not-found':
                    case 'auth/user-disabled':
                    case 'auth/invalid-email':
                        dispatch(auth_signInEmailErrorInvalidEmail());
                        dispatch(auth_signInAddInvalidEmail(action.payload.email));
                        break;

                    case 'auth/wrong-password':
                        dispatch(auth_signInEmailErrorWrongPassword());
                        break;

                    default:
                        dispatch(auth_signInEmailOtherError(error));
                        break;
                }
                dispatch(auth_resetUser());
            })
            .finally(() => {
                dispatch(appNotLoading());
            });

    },
    [AUTH_GO_TO_TARGET_STATE]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {
        let targetState: TargetState = state.app.auth.targetState;

        if (targetState && targetState.state() && targetState.state().name !== 'app') {
            let state = targetState.state();
            let params = targetState.params();
            let options = {reload: true, location: true};
            dispatch(stateGo(state.name, params, options))
        } else {
            dispatch(stateGo('main'));
        }
    },

    [AUTH_SIGN_IN_FACEBOOK]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {
        signInFacebookHelper(dispatch);
    },
    [AUTH_SIGN_IN_FACEBOOK_WITH_TOKEN]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {
        signInFacebookHelper(dispatch, action.payload);
    },
    [AUTH_SIGN_IN_FACEBOOK_CORDOVA]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {
        signInFacebookHelperCordova(dispatch);
    },
    [AUTH_SIGN_IN_FACEBOOK_WITH_TOKEN_CORDOVA]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {
        signInFacebookHelperCordova(dispatch, action.payload);
    },
    [AUTH_SIGN_IN_GOOGLE_LOGIN]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {
        signInGoogleLoginHelper(dispatch);
    },
    [AUTH_SIGN_IN_GOOGLE_WITH_TOKEN]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {
        signInGoogleLoginHelper(dispatch, action.payload);
    },
    [AUTH_SIGN_IN_GOOGLE_LOGIN_CORDOVA]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {
        signInGoogleLoginHelperCordova(dispatch);
    },
    [AUTH_SIGN_IN_GOOGLE_WITH_TOKEN_CORDOVA]: (action: ActionWPayload, dispatch: Dispatch<IGlobalAppState>, state: IGlobalAppState) => {
        signInGoogleLoginHelperCordova(dispatch, action.payload);
    },
};

function signInFacebookHelper(dispatch, oAuthResponse = null) {
    let firebaseService = <FirebaseService>injector.get('firebaseService');

    firebaseService.signInFB(oAuthResponse)
        .then(user => {
            if (user) {
                dispatch(auth_signInFacebookSuccess());
                dispatch(auth_setUser(user));
                dispatch(auth_goToTargetState());
                setTimeout(() => {
                    dispatch(auth_setUserAlreadyLoggedIn());
                }, 1000);
            }
        })
        .catch(error => {
            dispatch(auth_signInFacebookError(error));
            dispatch(auth_resetUser());
        })
        .finally(() => {
            dispatch(appNotLoading());
            dispatch(facebookNotLoading());
        })
}

function signInFacebookHelperCordova(dispatch, oAuthResponse = null) {
    let firebaseService = <FirebaseService>injector.get('firebaseService');

    firebaseService.signInFBCordova(oAuthResponse)
        .then(user => {
            if (user) {
                dispatch(auth_signInFacebookSuccessCordova());
                dispatch(auth_setUser(user));
                dispatch(auth_goToTargetState());
                setTimeout(() => {
                    dispatch(auth_setUserAlreadyLoggedIn());
                }, 1000);
            }
        })
        .catch(error => {
            // can happen if user have remove authorization from facebook.com
            // the token is still cached in the plugin, so we need to sign out
            if (error.code === "auth/internal-error") {
                // sign out from firebase, facebook plugin, and google login plugin
                dispatch(auth_signOut());
                // retry to login
                setTimeout(() => {
                    dispatch(auth_signInFacebookCordova());
                })
            } else if (error.status === "unknown") {
                // unknown is returned when pop up is closed with no action
                dispatch(auth_signInFacebookCancelledCordova());
            } else {
                dispatch(auth_signInFacebookErrorCordova(error));
            }

            dispatch(auth_resetUser());
        })
        .finally(() => {
            dispatch(appNotLoading());
            dispatch(facebookNotLoading());
        })
}

function signInGoogleLoginHelper(dispatch, GoogleUser: GoogleLoginAuthResponseModel = null) {
    let firebaseService = <FirebaseService>injector.get('firebaseService');

    firebaseService.signInGoogle(GoogleUser)
        .then(user => {
            if (user) {
                dispatch(auth_signInGoogleLoginSuccess());
                dispatch(auth_setUser(user));
                dispatch(auth_goToTargetState());
                setTimeout(() => {
                    dispatch(auth_setUserAlreadyLoggedIn());
                }, 1000);
            }
        })
        .catch(error => {
            dispatch(auth_signInGoogleLoginError(error));
            dispatch(auth_resetUser());
        })
        .finally(() => {
            dispatch(appNotLoading());
            dispatch(googleLoginNotLoading());
        })
}

function signInGoogleLoginHelperCordova(dispatch, pluginAuthResponse: GoogleLoginPluginAuthResposeModel = null) {
    let firebaseService = <FirebaseService>injector.get('firebaseService');

    firebaseService.signInGoogleCordova(pluginAuthResponse)
        .then(user => {
            if (user) {
                dispatch(auth_signInGoogleLoginSuccessCordova());
                dispatch(auth_setUser(user));
                dispatch(auth_goToTargetState());
                setTimeout(() => {
                    dispatch(auth_setUserAlreadyLoggedIn());
                }, 1000);
            }
        })
        .catch(error => {
            dispatch(auth_signInGoogleLoginErrorCordova(error));
            dispatch(auth_resetUser());
        })
        .finally(() => {
            dispatch(appNotLoading());
            dispatch(googleLoginNotLoading());
        })
}



