import { BaseController } from "../../../base.controller";
import { TransitionService } from "ui-router-core";
import { OnScreenNotificationsService } from "../../../services/unpure/on-screen-notifications.service";
import { ContentfulService } from "../../../services/pure/contentful.service";
import { LoggingService } from "../../../services/pure/logging.service";
import { INgRedux } from "ng-redux";

import ILocationService = angular.ILocationService;
import IAnchorScrollService = angular.IAnchorScrollService;
import {
    events_setSelectedEvent, events_showUpcomingEvents, events_showPastEvents,
    events_getEventsFromContentful, events_goToSelectedEvent
} from "../../../redux/events/events.action-creators";

declare var angular: angular.IAngularStatic;


export class EventsController extends BaseController {

    constructor($scope: angular.IScope,
                $timeout: angular.ITimeoutService,
                $ngRedux: INgRedux,
                $transitions: TransitionService,
                public onScreenNotificationsService: OnScreenNotificationsService,
                public contentfulService: ContentfulService,
                public loggingService: LoggingService,
                public $location: ILocationService,
                public $element: JQuery) {
        super($scope, $timeout, $ngRedux, $transitions);
    }

    $onInit() {
        this.$timeout(() => {
            this.initEventsList();
        }, 1200);
    }

    selectEvent(event) {
        this.$ngRedux.dispatch(events_setSelectedEvent(event));
        this.$ngRedux.dispatch(events_goToSelectedEvent());
    }

    showUpcomingEvents() {
        this.$ngRedux.dispatch(events_showUpcomingEvents());
    }

    showPastEvents() {
        this.$ngRedux.dispatch(events_showPastEvents());
    }

    initEventsList() {
        if (!this.$ngRedux.getState().app.events.events.length) {
            this.$ngRedux.dispatch(events_getEventsFromContentful(this.$ngRedux.getState().app.events.eventsSkip, 10));
        }
    }

    loadMore() {
        this.$ngRedux.dispatch(events_getEventsFromContentful(this.$ngRedux.getState().app.events.eventsSkip, 10));
    }
}

EventsController.$inject = [
    ...BaseController.injects,
    'onScreenNotificationsService',
    'contentfulService',
    'loggingService',
    '$location',
    '$element'
];




