import IQService = angular.IQService;
import IFilterService = angular.IFilterService;

export class FirebaseAnalyticsService {

    constructor(public $q: IQService) {
    }

    //================================================================
    // CORDOVA
    //================================================================

    sendEvent() {
        if (this.getFirebasePluginGlobal()) {
            this.getFirebasePluginGlobal().logEvent("select_content", {
                content_type: "page_view",
                item_id: "home"
            });
        }
    }

    setScreenName() {
        this.getFirebasePluginGlobal()
            .setScreenName("Home");
    }

    setUserId() {
        this.getFirebasePluginGlobal()
            .setUserId("user_id");
    }

    setUserProperty() {
        this.getFirebasePluginGlobal()
            .setUserProperty("name", "value");
    }

    getFirebasePluginGlobal() {
        if (window['FirebasePlugin']) {
            return window['FirebasePlugin'];
        }
    }

}

FirebaseAnalyticsService.$inject = ['$q'];





