import { Action } from "redux";

import {
    CONTENTFUL_INIT_WEB, CONTENTFUL_INIT_WEB_SUCCESS,
    CONTENTFUL_LOADING, CONTENTFUL_NOT_LOADING, CONTENTFUL_RESET_CLIENT,
    CONTENTFUL_SET_CLIENT, CONTENTFUL_INIT_WEB_ERROR
} from "./contentful.actions";

import { ActionWPayload } from "../../interfaces/redux-action-w-payload.interface";

export function contentfulLoading(): Action {
    return {
        type: CONTENTFUL_LOADING
    }
}

export function contentfulNotLoading(): Action {
    return {
        type: CONTENTFUL_NOT_LOADING
    }
}

export function contentfulInitWeb(): Action {
    return {
        type: CONTENTFUL_INIT_WEB
    }
}

export function contentfulInitWebSuccess(): Action {
    return {
        type: CONTENTFUL_INIT_WEB_SUCCESS
    }
}

export function contentfulInitWebError(error): ActionWPayload {
    return {
        type: CONTENTFUL_INIT_WEB_ERROR,
        payload: error
    }
}

export function contentfulSetClient(contentfulClient): ActionWPayload {
    return {
        type: CONTENTFUL_SET_CLIENT,
        payload: contentfulClient
    }
}

export function contentfulResetClient(): Action {
    return {
        type: CONTENTFUL_RESET_CLIENT
    }
}


