import IQService = angular.IQService;
import ILogService = angular.ILogService;
import IFilterService = angular.IFilterService;

import { OnScreenNotificationsService } from "../unpure/on-screen-notifications.service";
import { PushNotificationsService } from "../unused/push-notifications.service";
import { LogEventModel } from "../../models/log-event.model";
import { DeviceService } from "./device.service";
import { AnalyticsService } from "./analytics.service";
import { ILogEventOptions } from "../../interfaces/log-event-option.interface";

export class LoggingService {

    constructor(public $q: IQService,
                public onScreenNotificationsService: OnScreenNotificationsService,
                public pushNotificationsService: PushNotificationsService,
                public analyticsService: AnalyticsService,
                public deviceService: DeviceService,
                public $log: ILogService) {
    }

    log(logEvent: ILogEventOptions | LogEventModel) {

        let log = new LogEventModel(logEvent);

        // on screen
        if (log.displayOnScreen) {
            this.onScreenNotificationsService.publishFromLogEvent(log);
        }

        // analytics reporting
        if (log.level > 1) {
            this.analyticsService.log(log);
        }
    }
}

LoggingService.$inject = [
    '$q',
    'onScreenNotificationsService',
    'pushNotificationsService',
    'analyticsService',
    'deviceService',
    '$log',
];



